import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';

const SearchContainer = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 20px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 4px 20px rgba(0,0,0,0.1)' : 'none'};
  position: relative;
`;

const SearchInput = styled.input`
  width: 100%;
  background: transparent;
  border: none;
  color: ${props => props.theme.colors.text};
  font-size: 1.2rem;
  outline: none;
  padding: 0.5rem 0;
  
  &::placeholder {
    color: ${props => props.theme.colors.textSecondary};
  }
`;

const SearchResults = styled(motion.div)`
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: ${props => props.theme.gradients.card};
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 15px;
  margin-top: 0.5rem;
  max-height: 400px;
  overflow-y: auto;
  z-index: 1000;
  box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 8px 25px rgba(0,0,0,0.15)' : '0 8px 25px rgba(0,212,255,0.2)'};
`;

const ResultItem = styled(motion.div)`
  padding: 1rem;
  border-bottom: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.05)' : 'rgba(255,255,255,0.05)'};
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1rem;
  
  &:hover {
    background: ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.03)' : 'rgba(255,255,255,0.05)'};
  }
  
  .icon {
    font-size: 1.5rem;
  }
  
  .content {
    flex: 1;
    
    .title {
      color: ${props => props.theme.colors.text};
      font-weight: 600;
      margin-bottom: 0.25rem;
    }
    
    .description {
      color: ${props => props.theme.colors.textSecondary};
      font-size: 0.9rem;
    }
  }
`;

const LoadingSpinner = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  
  .spinner {
    width: 24px;
    height: 24px;
    border: 2px solid ${props => props.theme.colors.textSecondary};
    border-top: 2px solid ${props => props.theme.colors.primary};
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
`;

interface AISearchProps {
  onResultSelect?: (result: any) => void;
}

const AISearch: React.FC<AISearchProps> = ({ onResultSelect }) => {
  const [query, setQuery] = useState('');
  const [results, setResults] = useState<any[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showResults, setShowResults] = useState(false);

  const searchWithAI = async (searchQuery: string) => {
    if (!searchQuery.trim()) {
      setResults([]);
      setShowResults(false);
      return;
    }

    setIsLoading(true);
    setShowResults(true);

    try {
      // Get AI-powered search results
      const aiResults = await getAISearchResults(searchQuery);
      
      // Get image results
      const imageResults = await getImageResults(searchQuery);
      
      // Combine results
      const combinedResults = [
        ...aiResults,
        ...imageResults
      ];

      setResults(combinedResults);
    } catch (error) {
      console.error('Search error:', error);
      setResults([]);
    } finally {
      setIsLoading(false);
    }
  };

  const getAISearchResults = async (query: string) => {
    try {
      const response = await fetch('https://api.groq.com/openai/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${process.env.REACT_APP_GROQ_API_KEY}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          messages: [{
            role: 'user',
            content: `Provide 3 travel suggestions for: "${query}". Format as JSON array with fields: title, description, type, icon. Types: destination, activity, restaurant, hotel. Use appropriate emojis for icons.`
          }],
          model: 'mixtral-8x7b-32768',
          max_tokens: 300,
          temperature: 0.7
        })
      });

      if (response.ok) {
        const data = await response.json();
        const aiText = data.choices[0]?.message?.content || '[]';
        
        try {
          const parsed = JSON.parse(aiText);
          return Array.isArray(parsed) ? parsed : [];
        } catch {
          // Fallback AI results
          return [
            {
              title: `Explore ${query}`,
              description: 'AI-powered travel recommendations',
              type: 'destination',
              icon: ''
            }
          ];
        }
      }
    } catch (error) {
      console.error('AI search error:', error);
    }

    return [];
  };

  const getImageResults = async (query: string) => {
    try {
      const response = await fetch(
        `https://api.unsplash.com/search/photos?query=${encodeURIComponent(query + ' travel')}&per_page=2&client_id=${process.env.REACT_APP_UNSPLASH_ACCESS_KEY}`
      );

      if (response.ok) {
        const data = await response.json();
        return data.results?.map((photo: any) => ({
          title: photo.alt_description || query,
          description: `Photo by ${photo.user.name}`,
          type: 'image',
          icon: '',
          image: photo.urls.small
        })) || [];
      }
    } catch (error) {
      console.error('Image search error:', error);
    }

    return [];
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setQuery(value);
    
    // Debounce search
    setTimeout(() => {
      if (value === query) {
        searchWithAI(value);
      }
    }, 500);
  };

  const handleResultClick = (result: any) => {
    setShowResults(false);
    setQuery(result.title);
    onResultSelect?.(result);
  };

  return (
    <SearchContainer
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
    >
      <SearchInput
        type="text"
        placeholder=" Ask AI: 'Best places in Paris', 'Budget hotels Tokyo', 'Routes to airport'..."
        value={query}
        onChange={handleInputChange}
        onFocus={() => query && setShowResults(true)}
        onBlur={() => setTimeout(() => setShowResults(false), 200)}
      />
      
      <AnimatePresence>
        {showResults && (
          <SearchResults
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.2 }}
          >
            {isLoading ? (
              <LoadingSpinner>
                <div className="spinner" />
                <span style={{ marginLeft: '0.5rem', color: '#888' }}>AI is searching...</span>
              </LoadingSpinner>
            ) : results.length > 0 ? (
              results.map((result, index) => (
                <ResultItem
                  key={index}
                  onClick={() => handleResultClick(result)}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="icon">{result.icon}</div>
                  <div className="content">
                    <div className="title">{result.title}</div>
                    <div className="description">{result.description}</div>
                  </div>
                  {result.image && (
                    <img 
                      src={result.image} 
                      alt={result.title}
                      style={{ width: '50px', height: '50px', objectFit: 'cover', borderRadius: '8px' }}
                    />
                  )}
                </ResultItem>
              ))
            ) : query && (
              <ResultItem onClick={() => {}}>
                <div className="icon"></div>
                <div className="content">
                  <div className="title">No results found</div>
                  <div className="description">Try a different search term</div>
                </div>
              </ResultItem>
            )}
          </SearchResults>
        )}
      </AnimatePresence>
    </SearchContainer>
  );
};

export default AISearch;