import React, { useState, useEffect } from 'react';
import styled, { ThemeProvider, createGlobalStyle } from 'styled-components';
import { motion } from 'framer-motion';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import WelcomePage from './components/WelcomePage';
import LoginPage from './components/LoginPage';
import Dashboard from './components/Dashboard';
import ChatBot from './components/ChatBot';
import Navigation from './components/Navigation';
import ProfilePage from './pages/ProfilePage';
import ExplorePage from './pages/ExplorePage';
import RoutesPage from './pages/RoutesPage';
import NearbyPage from './pages/NearbyPage';
import WeatherPage from './pages/WeatherPage';
import FavoritesPage from './pages/FavoritesPage';
import ChatPage from './pages/ChatPage';
import BudgetPage from './pages/BudgetPage';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { ThemeProvider as CustomThemeProvider, useTheme } from './contexts/ThemeContext';
import { LocationProvider, useLocation } from './contexts/LocationContext';
import { useSessionTimeout } from './hooks/useSessionTimeout';
import SessionManager from './components/SessionManager';

const GlobalStyle = createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    background: ${props => props.theme.gradients.dark};
    color: ${props => props.theme.colors.text};
    overflow-x: hidden;
    transition: background 0.3s ease, color 0.3s ease;
  }

  @import url('https://unpkg.com/boxicons@2.1.4/css/boxicons.min.css');
`;

const lightTheme = {
  colors: {
    primary: '#1e40af',
    secondary: '#dc2626',
    accent: '#0ea5e9',
    dark: '#ffffff',
    darkBlue: '#f8fafc',
    navy: '#e2e8f0',
    text: '#0f172a',
    textSecondary: '#334155'
  },
  gradients: {
    primary: 'linear-gradient(135deg, #1e40af 0%, #3b82f6 100%)',
    dark: 'linear-gradient(135deg, #ffffff 0%, #f8fafc 100%)',
    card: 'linear-gradient(145deg, #ffffff 0%, #f8fafc 100%)'
  }
};

const darkTheme = {
  colors: {
    primary: '#00d4ff',
    secondary: '#ff6b6b',
    accent: '#4ecdc4',
    dark: '#0c0c0c',
    darkBlue: '#1a1a2e',
    navy: '#16213e',
    text: '#ffffff',
    textSecondary: '#b8b8b8'
  },
  gradients: {
    primary: 'linear-gradient(135deg, #00d4ff 0%, #4ecdc4 100%)',
    dark: 'linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #16213e 100%)',
    card: 'linear-gradient(145deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%)'
  }
};

const AppContainer = styled.div`
  min-height: 100vh;
  position: relative;
`;

function AppContent() {
  const [showWelcome, setShowWelcome] = useState(true);
  const [navOpen, setNavOpen] = useState(window.innerWidth > 768);
  const { user, loading } = useAuth();
  const { isDark } = useTheme();
  const { requestLocation } = useLocation();
  
  const currentTheme = isDark ? darkTheme : lightTheme;
  
  // Request location permission after login
  useEffect(() => {
    if (user && !localStorage.getItem('locationAsked')) {
      const timer = setTimeout(() => {
        requestLocation();
        localStorage.setItem('locationAsked', 'true');
      }, 1000); // Wait 1 second after login
      return () => clearTimeout(timer);
    }
  }, [user, requestLocation]);
  
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 768) {
        setNavOpen(true);
      } else {
        setNavOpen(false);
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  
  // Session timeout disabled - no automatic logout
  useSessionTimeout();

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowWelcome(false);
    }, 3000);
    return () => clearTimeout(timer);
  }, []);

  if (loading) {
    return (
      <AppContainer>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          style={{ 
            display: 'flex', 
            justifyContent: 'center', 
            alignItems: 'center', 
            height: '100vh',
            fontSize: '1.5rem'
          }}
        >
          Loading IntelliTrip...
        </motion.div>
      </AppContainer>
    );
  }

  if (showWelcome) {
    return (
      <AppContainer>
        <WelcomePage />
      </AppContainer>
    );
  }

  if (!user) {
    return (
      <AppContainer>
        <LoginPage />
      </AppContainer>
    );
  }

  return (
    <ThemeProvider theme={currentTheme}>
      <GlobalStyle />
      <AppContainer>
        <SessionManager />
        <Navigation isOpen={navOpen} onToggle={() => setNavOpen(!navOpen)} />
        <Routes>
          <Route path="/" element={<Navigate to="/dashboard" replace />} />
          <Route path="/dashboard" element={<Dashboard />} />
          <Route path="/profile" element={<ProfilePage />} />
          <Route path="/explore" element={<ExplorePage />} />
          <Route path="/routes" element={<RoutesPage />} />
          <Route path="/nearby" element={<NearbyPage />} />
          <Route path="/budget" element={<BudgetPage />} />
          <Route path="/weather" element={<WeatherPage />} />
          <Route path="/favorites" element={<FavoritesPage />} />
          <Route path="/chat" element={<ChatPage />} />
        </Routes>
        <ChatBot />
      </AppContainer>
    </ThemeProvider>
  );
}

function App() {
  return (
    <ThemeProvider theme={darkTheme}>
      <Router>
        <AuthProvider>
          <CustomThemeProvider>
            <LocationProvider>
              <AppContent />
            </LocationProvider>
          </CustomThemeProvider>
        </AuthProvider>
      </Router>
    </ThemeProvider>
  );
}

export default App;