import axios from 'axios';

export interface TransportOption {
  mode: string;
  icon: string;
  name: string;
  duration: string;
  distance: string;
  cost?: string;
  emissions?: string;
  calories?: string;
}

export const getTransportOptions = async (
  origin: {lat: number, lng: number},
  destination: {lat: number, lng: number}
): Promise<TransportOption[]> => {
  try {
    const options: TransportOption[] = [];
    
    // Walking - Pedestrian paths and sidewalks
    const walkingRoute = await getDirections(origin, destination, 'walking');
    const walkingDistance = parseFloat(walkingRoute.distance.replace(' km', ''));
    options.push({
      mode: 'walking',
      icon: '',
      name: 'Walking',
      duration: walkingRoute.duration || '25 min',
      distance: walkingRoute.distance || '2.1 km',
      calories: `${Math.round(walkingDistance * 60)} cal`,
      emissions: '0g CO2'
    });

    // Driving - Roads and highways
    const drivingRoute = await getDirections(origin, destination, 'driving');
    const drivingDistance = parseFloat(drivingRoute.distance.replace(' km', ''));
    options.push({
      mode: 'driving',
      icon: '',
      name: 'Driving',
      duration: drivingRoute.duration || '8 min',
      distance: drivingRoute.distance || '2.1 km',
      cost: `$${(drivingDistance * 0.5 + 2).toFixed(2)}`,
      emissions: `${Math.round(drivingDistance * 200)}g CO2`
    });

    // Bicycling - Bike lanes and paths
    const bicyclingRoute = await getDirections(origin, destination, 'bicycling');
    const bicyclingDistance = parseFloat(bicyclingRoute.distance.replace(' km', ''));
    options.push({
      mode: 'bicycling',
      icon: '',
      name: 'Bicycling',
      duration: bicyclingRoute.duration || '12 min',
      distance: bicyclingRoute.distance || '2.3 km',
      calories: `${Math.round(bicyclingDistance * 35)} cal`,
      emissions: '0g CO2'
    });

    // Public Transit - Bus, train, metro
    const transitDistance = parseFloat(drivingRoute.distance.replace(' km', ''));
    const transitDuration = estimateTransitDuration(transitDistance);
    options.push({
      mode: 'transit',
      icon: '',
      name: 'Public Transit',
      duration: transitDuration,
      distance: drivingRoute.distance || '2.5 km',
      cost: `$${(2.50 + transitDistance * 0.15).toFixed(2)}`,
      emissions: `${Math.round(transitDistance * 45)}g CO2`
    });

    // Motorcycle - Similar to driving but faster
    const motorcycleRoute = await getDirections(origin, destination, 'motorcycle');
    const motorcycleDistance = parseFloat(motorcycleRoute.distance.replace(' km', ''));
    options.push({
      mode: 'motorcycle',
      icon: '',
      name: 'Motorcycle',
      duration: motorcycleRoute.duration || '6 min',
      distance: motorcycleRoute.distance || '2.1 km',
      cost: `$${(motorcycleDistance * 0.3 + 1.5).toFixed(2)}`,
      emissions: `${Math.round(motorcycleDistance * 120)}g CO2`
    });

    return options;
  } catch (error) {
    console.error('Transport options error:', error);
    return getMockTransportOptions();
  }
};

const getDirections = async (
  origin: {lat: number, lng: number},
  destination: {lat: number, lng: number},
  mode: string
) => {
  try {
    const profile = getOrsProfile(mode);
    const response = await axios.get(
      `https://api.openrouteservice.org/v2/directions/${profile}?api_key=${process.env.REACT_APP_OPENROUTE_API_KEY}&start=${origin.lng},${origin.lat}&end=${destination.lng},${destination.lat}`
    );

    if (response.data.features?.[0]) {
      const route = response.data.features[0];
      const properties = route.properties.segments[0];
      
      return {
        distance: `${(properties.distance / 1000).toFixed(1)} km`,
        duration: formatDuration(properties.duration)
      };
    }
  } catch (error) {
    console.log(`OpenRouteService failed for ${mode}`);
  }
  
  const distance = calculateDistance(origin.lat, origin.lng, destination.lat, destination.lng);
  const duration = estimateDuration(distance, mode);
  
  return {
    distance: `${distance.toFixed(1)} km`,
    duration: duration
  };
};

const getOrsProfile = (mode: string) => {
  switch (mode) {
    case 'walking': return 'foot-walking';
    case 'bicycling': return 'cycling-regular';
    case 'driving': return 'driving-car';
    case 'motorcycle': return 'driving-car'; // Use car routing for motorcycle
    default: return 'driving-car';
  }
};

const formatDuration = (seconds: number) => {
  const minutes = Math.round(seconds / 60);
  return minutes < 60 ? `${minutes} min` : `${Math.floor(minutes / 60)}h ${minutes % 60}m`;
};

const calculateDistance = (lat1: number, lon1: number, lat2: number, lon2: number) => {
  const R = 6371; // Earth's radius in km
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
            Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
            Math.sin(dLon/2) * Math.sin(dLon/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  return R * c;
};

const estimateDuration = (distance: number, mode: string) => {
  const speeds = {
    walking: 5,     // km/h
    driving: 40,    // km/h
    bicycling: 15,  // km/h
    transit: 25,    // km/h
    taxi: 35,       // km/h
    motorcycle: 45  // km/h
  };
  
  const speed = speeds[mode as keyof typeof speeds] || speeds.driving;
  const minutes = Math.round((distance / speed) * 60);
  
  if (minutes < 60) {
    return `${minutes} min`;
  } else {
    const hours = Math.floor(minutes / 60);
    const remainingMinutes = minutes % 60;
    return `${hours}h ${remainingMinutes}m`;
  }
};

const estimateTransitDuration = (distance: number) => {
  const baseTime = (distance / 25) * 60;
  const waitingTime = 8;
  const transferTime = distance > 5 ? 5 : 0;
  
  const totalMinutes = Math.round(baseTime + waitingTime + transferTime);
  return totalMinutes < 60 ? `${totalMinutes} min` : `${Math.floor(totalMinutes / 60)}h ${totalMinutes % 60}m`;
};

