import axios from 'axios';

export interface WeatherData {
  location: string;
  temperature: number;
  feelsLike: number;
  description: string;
  humidity: number;
  windSpeed: number;
  icon: string;
}

export interface WeatherForecast {
  date: string;
  temperature: { min: number; max: number };
  description: string;
  icon: string;
}

const validateCoordinates = (lat: number, lon: number): void => {
  if (typeof lat !== 'number' || typeof lon !== 'number') {
    throw new Error('Invalid coordinates: must be numbers');
  }
  if (lat < -90 || lat > 90) {
    throw new Error('Invalid latitude: must be between -90 and 90');
  }
  if (lon < -180 || lon > 180) {
    throw new Error('Invalid longitude: must be between -180 and 180');
  }
};

const sanitizeString = (str: string): string => {
  return str.replace(/[<>"'&]/g, '');
};

export const getCurrentWeather = async (lat: number, lon: number): Promise<WeatherData> => {
  validateCoordinates(lat, lon);
  
  const apiKey = process.env.REACT_APP_OPENWEATHER_API_KEY;
  if (!apiKey) {
    throw new Error('Weather API key not configured');
  }

  try {
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/weather`,
      {
        params: {
          lat: lat.toFixed(6),
          lon: lon.toFixed(6),
          appid: apiKey,
          units: 'metric'
        },
        timeout: 10000
      }
    );

    if (!response.data || !response.data.main || !response.data.weather?.[0]) {
      throw new Error('Invalid weather data received');
    }

    return {
      location: sanitizeString(response.data.name || 'Unknown'),
      temperature: Math.round(Number(response.data.main.temp) || 0),
      feelsLike: Math.round(Number(response.data.main.feels_like) || 0),
      description: sanitizeString(response.data.weather[0].description || ''),
      humidity: Number(response.data.main.humidity) || 0,
      windSpeed: Number(response.data.wind?.speed) || 0,
      icon: sanitizeString(response.data.weather[0].icon || '')
    };
  } catch (error) {
    throw new Error('Failed to fetch weather data');
  }
};

export const getWeatherForecast = async (lat: number, lon: number): Promise<WeatherForecast[]> => {
  validateCoordinates(lat, lon);
  
  const apiKey = process.env.REACT_APP_OPENWEATHER_API_KEY;
  if (!apiKey) {
    throw new Error('Weather API key not configured');
  }

  try {
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/forecast`,
      {
        params: {
          lat: lat.toFixed(6),
          lon: lon.toFixed(6),
          appid: apiKey,
          units: 'metric'
        },
        timeout: 10000
      }
    );

    if (!response.data?.list || !Array.isArray(response.data.list)) {
      throw new Error('Invalid forecast data received');
    }

    const dailyForecasts: WeatherForecast[] = [];
    const processedDates = new Set<string>();

    response.data.list.forEach((item: any) => {
      if (!item?.dt || !item?.main || !item?.weather?.[0]) return;
      
      const date = new Date(item.dt * 1000).toDateString();
      
      if (!processedDates.has(date) && dailyForecasts.length < 5) {
        processedDates.add(date);
        dailyForecasts.push({
          date: new Date(item.dt * 1000).toLocaleDateString('en-US', { 
            weekday: 'short', 
            month: 'short', 
            day: 'numeric' 
          }),
          temperature: {
            min: Math.round(Number(item.main.temp_min) || 0),
            max: Math.round(Number(item.main.temp_max) || 0)
          },
          description: sanitizeString(item.weather[0].description || ''),
          icon: sanitizeString(item.weather[0].icon || '')
        });
      }
    });

    return dailyForecasts;
  } catch (error) {
    throw new Error('Failed to fetch weather forecast');
  }
};

export const getWeatherForMultipleLocations = async (locations: {lat: number, lon: number, name: string}[]): Promise<WeatherData[]> => {
  if (!Array.isArray(locations) || locations.length === 0) {
    return [];
  }

  if (locations.length > 10) {
    throw new Error('Too many locations requested');
  }

  try {
    const weatherPromises = locations.map(async (location) => {
      if (!location || typeof location.name !== 'string') {
        throw new Error('Invalid location data');
      }
      
      const weather = await getCurrentWeather(location.lat, location.lon);
      return { ...weather, location: sanitizeString(location.name) };
    });

    return await Promise.all(weatherPromises);
  } catch (error) {
    throw new Error('Failed to fetch weather for multiple locations');
  }
};

export const getWeatherAlerts = async (lat: number, lon: number): Promise<any[]> => {
  validateCoordinates(lat, lon);
  
  const apiKey = process.env.REACT_APP_OPENWEATHER_API_KEY;
  if (!apiKey) {
    throw new Error('Weather API key not configured');
  }

  try {
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/onecall`,
      {
        params: {
          lat: lat.toFixed(6),
          lon: lon.toFixed(6),
          appid: apiKey,
          units: 'metric'
        },
        timeout: 10000
      }
    );

    return Array.isArray(response.data?.alerts) ? response.data.alerts : [];
  } catch (error) {
    return [];
  }
};