import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useLocation } from '../contexts/LocationContext';

import LocationPermission from '../components/LocationPermission';
import RealPlacesDisplay from '../components/RealPlacesDisplay';

const NearbyContainer = styled.div`
  min-height: 100vh;
  background: ${props => props.theme.gradients.dark};
  padding: 2rem;
  margin-left: 280px;
  
  @media (max-width: 768px) {
    margin-left: 0;
  }
`;

const Header = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 4px 20px rgba(0,0,0,0.1)' : 'none'};
  
  h2 {
    color: ${props => props.theme.colors.text} !important;
    margin-bottom: 0.5rem;
  }
  
  p {
    color: ${props => props.theme.colors.textSecondary} !important;
    margin-bottom: 1rem;
  }
  
  .filters {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
    flex-wrap: wrap;
  }
  
  .filter-btn {
    padding: 0.5rem 1rem;
    border-radius: 20px;
    border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.2)' : 'rgba(255,255,255,0.2)'};
    background: transparent;
    color: ${props => props.theme.colors.text};
    cursor: pointer;
    transition: all 0.3s;
    
    &.active {
      background: ${props => props.theme.colors.primary};
      border-color: ${props => props.theme.colors.primary};
      color: white;
    }
    
    &:hover {
      background: ${props => props.theme.colors.text === '#0f172a' ? 'rgba(30,64,175,0.1)' : 'rgba(0,212,255,0.2)'};
    }
  }
`;

const PlaceDetails = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 20px;
  padding: 2rem;
  margin-top: 2rem;
  box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 4px 20px rgba(0,0,0,0.1)' : 'none'};
  
  .place-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1rem;
    
    .place-info {
      .name {
        color: ${props => props.theme.colors.text};
        font-size: 1.5rem;
        font-weight: 600;
        margin-bottom: 0.5rem;
      }
      
      .address {
        color: ${props => props.theme.colors.textSecondary};
        margin-bottom: 0.5rem;
      }
      
      .rating {
        color: ${props => props.theme.colors.accent};
        font-weight: 600;
      }
    }
    
    .actions {
      display: flex;
      gap: 0.5rem;
      
      button {
        background: ${props => props.theme.colors.primary};
        border: none;
        border-radius: 8px;
        padding: 0.5rem 1rem;
        color: white;
        cursor: pointer;
        font-size: 0.9rem;
        
        &:hover {
          background: ${props => props.theme.colors.accent};
        }
      }
    }
  }
`;

const NearbyPage: React.FC = () => {
  const { location, hasPermission } = useLocation();
  const [showLocationPermission, setShowLocationPermission] = useState(false);
  const [selectedPlace, setSelectedPlace] = useState<any>(null);
  const [activeFilter, setActiveFilter] = useState('tourist_attraction');
  const [mapView, setMapView] = useState<'normal' | 'satellite'>('normal');
  const [nearbyPlaces, setNearbyPlaces] = useState<any[]>([]);

  const filters = [
    { id: 'tourist_attraction', label: ' Attractions', type: 'tourist_attraction' },
    { id: 'restaurant', label: ' Restaurants', type: 'restaurant' },
    { id: 'museum', label: ' Museums', type: 'museum' },
    { id: 'park', label: ' Parks', type: 'park' },
    { id: 'shopping_mall', label: ' Shopping', type: 'shopping_mall' },
    { id: 'lodging', label: ' Hotels', type: 'lodging' },
    { id: 'gas_station', label: ' Gas Stations', type: 'gas_station' },
    { id: 'hospital', label: ' Healthcare', type: 'hospital' }
  ];

  useEffect(() => {
    if (!hasPermission && !location) {
      setTimeout(() => setShowLocationPermission(true), 500);
    }
  }, [hasPermission, location]);



  const handlePlaceSelect = (place: any) => {
    setSelectedPlace(place);
  };

  const handlePlacesUpdate = (places: any[]) => {
    setNearbyPlaces(places);
  };



  const getDirections = () => {
    if (selectedPlace && location) {
      let destLat, destLng;
      
      if (selectedPlace.geometry && selectedPlace.geometry.location) {
        if (typeof selectedPlace.geometry.location.lat === 'function') {
          destLat = selectedPlace.geometry.location.lat();
          destLng = selectedPlace.geometry.location.lng();
        } else {
          destLat = selectedPlace.geometry.location.lat;
          destLng = selectedPlace.geometry.location.lng;
        }
      } else {
        destLat = location.lat + 0.01;
        destLng = location.lng + 0.01;
      }
      
      const url = `https://www.google.com/maps/dir/${location.lat},${location.lng}/${destLat},${destLng}`;
      window.open(url, '_blank');
    }
  };

  const savePlace = () => {
    if (selectedPlace) {
      const savedPlaces = JSON.parse(localStorage.getItem('savedPlaces') || '[]');
      savedPlaces.push({
        id: selectedPlace.id,
        name: selectedPlace.name,
        vicinity: selectedPlace.vicinity,
        rating: selectedPlace.rating
      });
      localStorage.setItem('savedPlaces', JSON.stringify(savedPlaces));
      alert('Place saved to favorites!');
    }
  };

  return (
    <NearbyContainer>
      <Header
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <h2 style={{ color: 'white', marginBottom: '0.5rem' }}> Nearby Places</h2>
        <p style={{ color: '#b8b8b8', marginBottom: '1rem' }}>
          Discover interesting places around your location
        </p>
        
        <div className="filters">
          {filters.map(filter => (
            <button
              key={filter.id}
              className={`filter-btn ${activeFilter === filter.id ? 'active' : ''}`}
              onClick={() => setActiveFilter(filter.id)}
            >
              {filter.label}
            </button>
          ))}
        </div>
      </Header>

      {location && (
        <>
          <motion.div
            style={{ 
              marginBottom: '2rem',
              position: 'relative',
              height: '500px',
              borderRadius: '20px',
              overflow: 'hidden',
              border: '2px solid rgba(0,212,255,0.3)',
              boxShadow: '0 20px 40px rgba(0,0,0,0.4)'
            }}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.3 }}
          >
            <div style={{
              position: 'absolute',
              top: '10px',
              right: '10px',
              zIndex: 1000,
              display: 'flex',
              gap: '0.5rem'
            }}>
              <button
                onClick={() => setMapView('normal')}
                style={{
                  background: mapView === 'normal' ? '#00d4ff' : 'rgba(255,255,255,0.8)',
                  color: mapView === 'normal' ? 'white' : 'black',
                  border: 'none',
                  borderRadius: '5px',
                  padding: '0.5rem',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: '600'
                }}
              >
                 Map
              </button>
              <button
                onClick={() => setMapView('satellite')}
                style={{
                  background: mapView === 'satellite' ? '#00d4ff' : 'rgba(255,255,255,0.8)',
                  color: mapView === 'satellite' ? 'white' : 'black',
                  border: 'none',
                  borderRadius: '5px',
                  padding: '0.5rem',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: '600'
                }}
              >
                 Satellite
              </button>
            </div>
            
            <iframe
              key={`${activeFilter}_${nearbyPlaces.length}_${mapView}`}
              src={(() => {
                const baseUrl = mapView === 'normal' 
                  ? `https://www.openstreetmap.org/export/embed.html?bbox=${(location.lng - 0.05).toFixed(4)},${(location.lat - 0.05).toFixed(4)},${(location.lng + 0.05).toFixed(4)},${(location.lat + 0.05).toFixed(4)}&layer=mapnik`
                  : `https://www.openstreetmap.org/export/embed.html?bbox=${(location.lng - 0.05).toFixed(4)},${(location.lat - 0.05).toFixed(4)},${(location.lng + 0.05).toFixed(4)},${(location.lat + 0.05).toFixed(4)}&layer=hot`;
                
                // Add user location marker (blue)
                let markers = `&marker=${location.lat.toFixed(4)},${location.lng.toFixed(4)}`;
                
                // Add nearby places markers
                if (nearbyPlaces && nearbyPlaces.length > 0) {
                  nearbyPlaces.slice(0, 8).forEach((place, index) => {
                    if (place && place.geometry && place.geometry.location) {
                      try {
                        const lat = typeof place.geometry.location.lat === 'function' 
                          ? place.geometry.location.lat() 
                          : place.geometry.location.lat;
                        const lng = typeof place.geometry.location.lng === 'function'
                          ? place.geometry.location.lng()
                          : place.geometry.location.lng;
                        
                        if (lat && lng && !isNaN(lat) && !isNaN(lng)) {
                          markers += `&marker=${lat.toFixed(4)},${lng.toFixed(4)}`;
                        }
                      } catch (error) {
                        console.log(`Error processing marker ${index}:`, error);
                      }
                    }
                  });
                }
                
                console.log('Map URL:', baseUrl + markers);
                return baseUrl + markers;
              })()
              }
              width="100%"
              height="100%"
              style={{
                border: 'none',
                borderRadius: '15px'
              }}
              title="Nearby Places Map"
              loading="lazy"
            />
          </motion.div>
          
          <motion.div
            style={{
              display: 'flex',
              gap: '1rem',
              justifyContent: 'center',
              marginBottom: '2rem'
            }}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.5 }}
          >
            <button
              onClick={() => {
                const destLat = selectedPlace?.geometry?.location?.lat?.() || location.lat + 0.01;
                const destLng = selectedPlace?.geometry?.location?.lng?.() || location.lng + 0.01;
                const url = `https://www.google.com/maps/dir/${location.lat},${location.lng}/${destLat},${destLng}`;
                window.open(url, '_blank');
              }}
              style={{
                background: 'linear-gradient(135deg, #00d4ff 0%, #4ecdc4 100%)',
                border: 'none',
                borderRadius: '25px',
                padding: '1rem 2rem',
                color: 'white',
                fontWeight: '600',
                cursor: 'pointer',
                fontSize: '1rem',
                display: 'flex',
                alignItems: 'center',
                gap: '0.5rem',
                transition: 'all 0.3s'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = 'translateY(-2px)';
                e.currentTarget.style.boxShadow = '0 8px 25px rgba(0,212,255,0.4)';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = 'translateY(0)';
                e.currentTarget.style.boxShadow = 'none';
              }}
            >
              <img 
                src="/images/maps.png" 
                alt="Google Maps" 
                style={{ width: '20px', height: '20px' }}
              />
              Open in Google Maps
            </button>
          </motion.div>
          
          <RealPlacesDisplay
            location={location}
            placeType={filters.find(f => f.id === activeFilter)?.type || 'tourist_attraction'}
            onPlaceClick={handlePlaceSelect}
            onPlacesUpdate={handlePlacesUpdate}
          />
        </>
      )}

      {selectedPlace && (
        <PlaceDetails
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
        >
          <div className="place-header">
            <div className="place-info">
              <div className="name">{selectedPlace.name}</div>
              <div className="address">{selectedPlace.vicinity}</div>
              {selectedPlace.rating && (
                <div className="rating">
                   {selectedPlace.rating} / 5.0
                </div>
              )}
            </div>
            <div className="actions">
              <button onClick={getDirections}> Directions</button>
              <button onClick={savePlace}> Save</button>
            </div>
          </div>
        </PlaceDetails>
      )}

      <LocationPermission
        isOpen={showLocationPermission}
        onClose={() => setShowLocationPermission(false)}
      />
    </NearbyContainer>
  );
};

export default NearbyPage;