interface LocationCoords {
  lat: number;
  lng: number;
}

interface Recommendation {
  id: string;
  name: string;
  description: string;
  category: string;
  rating: number;
  distance: string;
  coordinates: LocationCoords;
  image?: string;
}

export const getLocationBasedRecommendations = async (location: LocationCoords): Promise<Recommendation[]> => {
  // Determine region/city based on coordinates
  const region = getRegionFromCoordinates(location);
  
  // Get recommendations based on region
  const recommendations = getRecommendationsByRegion(region, location);
  
  return recommendations;
};

const getRegionFromCoordinates = (location: LocationCoords): string => {
  const { lat, lng } = location;
  
  // New York City area
  if (lat >= 40.4774 && lat <= 40.9176 && lng >= -74.2591 && lng <= -73.7004) {
    return 'nyc';
  }
  
  // Los Angeles area
  if (lat >= 33.7037 && lat <= 34.3373 && lng >= -118.6682 && lng <= -118.1553) {
    return 'la';
  }
  
  // London area
  if (lat >= 51.2867 && lat <= 51.6918 && lng >= -0.5103 && lng <= 0.3340) {
    return 'london';
  }
  
  // Paris area
  if (lat >= 48.8155 && lat <= 48.9021 && lng >= 2.2241 && lng <= 2.4699) {
    return 'paris';
  }
  
  // Default to generic recommendations
  return 'generic';
};

