// Smart Travel Assistant - No API Required
export const getSmartResponse = (message) => {
  const lowerMessage = message.toLowerCase();
  
  // Greeting responses
  if (lowerMessage.includes('hello') || lowerMessage.includes('hi') || lowerMessage.includes('hey')) {
    return "Hello! I'm your IntelliTrip assistant. I can help you with travel planning, destinations, budgets, weather, and more. What would you like to explore today?";
  }
  
  // Weather queries
  if (lowerMessage.includes('weather') || lowerMessage.includes('climate') || lowerMessage.includes('temperature')) {
    return "I can help you check weather conditions for any destination! Use the Weather section in the app to get real-time weather data, forecasts, and climate information for your travel dates.";
  }
  
  // Budget queries
  if (lowerMessage.includes('budget') || lowerMessage.includes('money') || lowerMessage.includes('cost') || lowerMessage.includes('cheap') || lowerMessage.includes('expensive')) {
    return "Great question about budget planning! Check out the Budget Recommendations section where I can suggest activities and places based on your spending limit. What's your budget range - 500, 1000, 2000, or 5000?";
  }
  
  // Places and attractions
  if (lowerMessage.includes('place') || lowerMessage.includes('attraction') || lowerMessage.includes('visit') || lowerMessage.includes('destination') || lowerMessage.includes('sightseeing')) {
    return "I'd love to help you discover amazing places! Use the Explore Destinations feature to find popular attractions, hidden gems, and must-visit spots. You can also check Nearby Places for attractions around your current location.";
  }
  
  // Food queries
  if (lowerMessage.includes('food') || lowerMessage.includes('restaurant') || lowerMessage.includes('eat') || lowerMessage.includes('cuisine') || lowerMessage.includes('dining')) {
    return "Foodie adventure awaits! I can suggest local cuisines and restaurants through the Budget Recommendations and Nearby Places features. What type of dining experience are you looking for - street food, fine dining, or local specialties?";
  }
  
  // Transportation
  if (lowerMessage.includes('transport') || lowerMessage.includes('flight') || lowerMessage.includes('train') || lowerMessage.includes('bus') || lowerMessage.includes('route') || lowerMessage.includes('direction')) {
    return "Getting around is easy with IntelliTrip! Use the Routes & Directions feature to plan your journey. I can help with directions, route planning, and transportation options between destinations.";
  }
  
  // Hotel/accommodation
  if (lowerMessage.includes('hotel') || lowerMessage.includes('stay') || lowerMessage.includes('accommodation') || lowerMessage.includes('booking')) {
    return "For accommodation, I recommend checking the Budget Recommendations section where you can find hotels and stays within your budget range. I can suggest options from budget-friendly to luxury experiences!";
  }
  
  // App features
  if (lowerMessage.includes('feature') || lowerMessage.includes('how to') || lowerMessage.includes('help') || lowerMessage.includes('guide')) {
    return "IntelliTrip has amazing features:  Weather forecasts,  Budget recommendations,  Routes & directions,  Nearby places,  Destination explorer, and  Favorites. Which feature would you like to explore?";
  }
  
  // Thank you
  if (lowerMessage.includes('thank') || lowerMessage.includes('thanks')) {
    return "You're welcome! I'm always here to help make your travel planning easier and more enjoyable. Have a wonderful trip! ";
  }
  
  // Default intelligent response
  return "I'm your intelligent travel assistant! I can help you with:\n\n Weather information\n Budget planning\n Finding nearby places\n Routes and directions\n Destination recommendations\n\nWhat aspect of your trip would you like to plan today?";
};

// Additional travel tips and suggestions
export const getTravelTips = () => {
  const tips = [
    " Tip: Check the weather forecast before packing to ensure you bring appropriate clothing!",
    " Tip: Set a daily budget to better manage your travel expenses and enjoy more activities!",
    " Tip: Explore nearby places to discover hidden gems that aren't in typical tourist guides!",
    " Tip: Save your favorite destinations in the app for quick access during your trip!",
    " Tip: Use the routes feature to plan efficient travel between multiple destinations!"
  ];
  
  return tips[Math.floor(Math.random() * tips.length)];
};