import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useLocation } from '../contexts/LocationContext';
import { getLocationBasedRecommendations } from '../services/recommendationsService';

const RecommendationsContainer = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 4px 20px rgba(0,0,0,0.1)' : 'none'};
`;

const Title = styled.h3`
  color: ${props => props.theme.colors.text};
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
`;

const PlacesGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
`;

const PlaceCard = styled(motion.div)`
  background: ${props => props.theme.colors.text === '#0f172a' ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.1)'};
  border-radius: 15px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s;
  border: 1px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  
  &:hover {
    transform: translateY(-5px);
    border-color: ${props => props.theme.colors.primary};
    box-shadow: ${props => props.theme.colors.text === '#0f172a' ? '0 8px 25px rgba(0,0,0,0.15)' : '0 8px 25px rgba(0,212,255,0.3)'};
  }
`;

const PlaceName = styled.h4`
  color: ${props => props.theme.colors.text};
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
`;

const PlaceInfo = styled.div`
  color: ${props => props.theme.colors.textSecondary};
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
`;

const PlaceRating = styled.div`
  color: ${props => props.theme.colors.accent};
  font-weight: 600;
  margin-bottom: 1rem;
`;

const ActionButton = styled.button`
  background: ${props => props.theme.colors.primary};
  color: white;
  border: none;
  border-radius: 8px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.9rem;
  margin-right: 0.5rem;
  
  &:hover {
    background: ${props => props.theme.colors.accent};
  }
`;

const LoadingSpinner = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  color: ${props => props.theme.colors.textSecondary};
`;

interface Place {
  place_id: string;
  name: string;
  vicinity: string;
  rating?: number;
  types: string[];
  geometry: {
    location: {
      lat: number;
      lng: number;
    };
  };
}

const LocationRecommendations: React.FC = () => {
  const { location } = useLocation();
  const [recommendations, setRecommendations] = useState<Place[]>([]);
  const [loading, setLoading] = useState(false);
  const [currentCity, setCurrentCity] = useState<string>('');

  const fetchRecommendations = async () => {
    if (!location) return;

    setLoading(true);
    try {
      // Get smart recommendations based on location
      const smartRecommendations = await getLocationBasedRecommendations(location);
      
      // Convert to Place format
      const places: Place[] = smartRecommendations.map(rec => ({
        place_id: rec.id,
        name: rec.name,
        vicinity: rec.description,
        rating: rec.rating,
        types: [rec.category.toLowerCase()],
        geometry: {
          location: rec.coordinates
        }
      }));
      
      setRecommendations(places);
      
      // Set city name based on location
      const cityName = getCityName(location);
      setCurrentCity(cityName);
      
    } catch (error) {
      console.error('Error fetching recommendations:', error);
      // Use fallback recommendations
      const fallbackPlaces = getFallbackRecommendations(location);
      setRecommendations(fallbackPlaces);
    } finally {
      setLoading(false);
    }
  };

  const getCityName = (coords: { lat: number; lng: number }): string => {
    const { lat, lng } = coords;
    
    if (lat >= 40.4774 && lat <= 40.9176 && lng >= -74.2591 && lng <= -73.7004) {
      return 'New York City';
    }
    if (lat >= 33.7037 && lat <= 34.3373 && lng >= -118.6682 && lng <= -118.1553) {
      return 'Los Angeles';
    }
    if (lat >= 51.2867 && lat <= 51.6918 && lng >= -0.5103 && lng <= 0.3340) {
      return 'London';
    }
    if (lat >= 48.8155 && lat <= 48.9021 && lng >= 2.2241 && lng <= 2.4699) {
      return 'Paris';
    }
    
    return 'Your Area';
  };

  const getFallbackRecommendations = (userLocation: { lat: number; lng: number }): Place[] => {
    // Generate recommendations based on location
    const isNYC = Math.abs(userLocation.lat - 40.7128) < 0.1 && Math.abs(userLocation.lng + 74.0060) < 0.1;
    
    if (isNYC) {
      return [
        {
          place_id: 'nyc1',
          name: 'Central Park',
          vicinity: 'Manhattan, NY',
          rating: 4.6,
          types: ['park'],
          geometry: { location: { lat: 40.7829, lng: -73.9654 } }
        },
        {
          place_id: 'nyc2',
          name: 'Times Square',
          vicinity: 'Manhattan, NY',
          rating: 4.3,
          types: ['tourist_attraction'],
          geometry: { location: { lat: 40.7580, lng: -73.9855 } }
        },
        {
          place_id: 'nyc3',
          name: 'Brooklyn Bridge',
          vicinity: 'New York, NY',
          rating: 4.5,
          types: ['tourist_attraction'],
          geometry: { location: { lat: 40.7061, lng: -73.9969 } }
        }
      ];
    }

    // Generic recommendations for other locations
    return [
      {
        place_id: 'generic1',
        name: 'Local Park',
        vicinity: 'Near you',
        rating: 4.2,
        types: ['park'],
        geometry: { location: { lat: userLocation.lat + 0.01, lng: userLocation.lng + 0.01 } }
      },
      {
        place_id: 'generic2',
        name: 'City Center',
        vicinity: 'Downtown area',
        rating: 4.0,
        types: ['tourist_attraction'],
        geometry: { location: { lat: userLocation.lat - 0.01, lng: userLocation.lng - 0.01 } }
      }
    ];
  };

  useEffect(() => {
    if (location) {
      fetchRecommendations();
    }
  }, [location]);

  const openInMaps = (place: Place) => {
    const url = `https://www.google.com/maps/search/?api=1&query=${place.geometry.location.lat},${place.geometry.location.lng}`;
    window.open(url, '_blank');
  };

  const getDirections = (place: Place) => {
    if (location) {
      const url = `https://www.google.com/maps/dir/${location.lat},${location.lng}/${place.geometry.location.lat},${place.geometry.location.lng}`;
      window.open(url, '_blank');
    }
  };

  if (!location) return null;

  return (
    <RecommendationsContainer
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
    >
      <Title>
         Recommended Places {currentCity && `in ${currentCity}`}
      </Title>
      
      {loading ? (
        <LoadingSpinner>
          <div> Finding amazing places near you...</div>
        </LoadingSpinner>
      ) : (
        <PlacesGrid>
          {recommendations.map((place, index) => (
            <PlaceCard
              key={place.place_id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.3, delay: index * 0.1 }}
              whileHover={{ scale: 1.02 }}
            >
              <PlaceName>{place.name}</PlaceName>
              <PlaceInfo> {place.vicinity}</PlaceInfo>
              {place.rating && (
                <PlaceRating> {place.rating}/5.0</PlaceRating>
              )}
              <div>
                <ActionButton onClick={() => openInMaps(place)}>
                   View
                </ActionButton>
                <ActionButton onClick={() => getDirections(place)}>
                   Directions
                </ActionButton>
              </div>
            </PlaceCard>
          ))}
        </PlacesGrid>
      )}
    </RecommendationsContainer>
  );
};

export default LocationRecommendations;