import React, { createContext, useContext, useState, useEffect } from 'react';

interface LocationContextType {
  location: { lat: number; lng: number } | null;
  isLoading: boolean;
  error: string | null;
  hasPermission: boolean;
  requestLocation: () => Promise<void>;
  clearLocation: () => void;
  resetAndRetry: () => Promise<void>;
}

const LocationContext = createContext<LocationContextType | undefined>(undefined);

export const useLocation = () => {
  const context = useContext(LocationContext);
  if (!context) {
    throw new Error('useLocation must be used within LocationProvider');
  }
  return context;
};

export const LocationProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [hasPermission, setHasPermission] = useState(false);

  const requestLocation = async () => {
    if (!navigator.geolocation) {
      setError('Geolocation not supported');
      setDefaultLocation();
      return;
    }

    setIsLoading(true);
    setError(null);

    // First attempt with high accuracy
    const tryGetLocation = (options: PositionOptions) => {
      return new Promise<GeolocationPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, options);
      });
    };

    try {
      // Try high accuracy first
      let position;
      try {
        position = await tryGetLocation({
          enableHighAccuracy: true,
          timeout: 5000,
          maximumAge: 0
        });
      } catch {
        // Fallback to low accuracy
        position = await tryGetLocation({
          enableHighAccuracy: false,
          timeout: 10000,
          maximumAge: 600000
        });
      }

      const coords = {
        lat: position.coords.latitude,
        lng: position.coords.longitude
      };
      
      setLocation(coords);
      setHasPermission(true);
      localStorage.setItem('userLocation', JSON.stringify(coords));
      localStorage.setItem('locationTimestamp', Date.now().toString());
      
    } catch (error: any) {
      console.error('All location attempts failed:', error);
      setError('Location access denied or unavailable');
      setHasPermission(false);
      setDefaultLocation();
    } finally {
      setIsLoading(false);
    }
  };

  const setDefaultLocation = () => {
    // Default to New York City
    const defaultCoords = { lat: 40.7128, lng: -74.0060 };
    setLocation(defaultCoords);
    localStorage.setItem('userLocation', JSON.stringify(defaultCoords));
  };

  const clearLocation = () => {
    setLocation(null);
    setHasPermission(false);
    setError(null);
    localStorage.removeItem('userLocation');
    localStorage.removeItem('locationTimestamp');
    localStorage.removeItem('locationAsked');
  };

  const resetAndRetry = async () => {
    clearLocation();
    await new Promise(resolve => setTimeout(resolve, 100));
    await requestLocation();
  };

  // Load saved location only
  useEffect(() => {
    const savedLocation = localStorage.getItem('userLocation');
    if (savedLocation) {
      try {
        const coords = JSON.parse(savedLocation);
        setLocation(coords);
        setHasPermission(true);
      } catch (err) {
        localStorage.removeItem('userLocation');
      }
    }
  }, []);

  return (
    <LocationContext.Provider value={{
      location,
      isLoading,
      error,
      hasPermission,
      requestLocation,
      clearLocation,
      resetAndRetry
    }}>
      {children}
    </LocationContext.Provider>
  );
};