const LoadingState = styled.div`
  text-align: center;
  padding: 2rem;
  color: ${props => props.theme.colors.textSecondary};
`;

interface BudgetRecommendationsProps {
  location: {lat: number, lng: number} | null;
}

const BudgetRecommendations: React.FC<BudgetRecommendationsProps> = ({ location }) => {
  const [selectedBudget, setSelectedBudget] = useState(1000);
  const [recommendations, setRecommendations] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [locationName, setLocationName] = useState('');
  const [manualLocation, setManualLocation] = useState('');
  const [currentLocation, setCurrentLocation] = useState(location);
  const [locationSuggestions, setLocationSuggestions] = useState<any[]>([]);
  const [showSuggestions, setShowSuggestions] = useState(false);


  const budgetOptions = [
    { value: 500, label: '500' },
    { value: 1000, label: '1000' },
    { value: 2000, label: '2000' },
    { value: 5000, label: '5000' }
  ];

  useEffect(() => {
    setCurrentLocation(location);
  }, [location]);



  useEffect(() => {
    if (currentLocation) {
      getLocationName();
      fetchRecommendations(selectedBudget);
    }
  }, [selectedBudget, currentLocation]);

  const getLocationName = async () => {
    try {
      const response = await axios.get(
        `https://api.openweathermap.org/geo/1.0/reverse?lat=${currentLocation?.lat}&lon=${currentLocation?.lng}&limit=1&appid=${process.env.REACT_APP_OPENWEATHER_API_KEY}`
      );
      setLocationName(response.data[0]?.name || 'your area');
    } catch (error) {
      setLocationName('your area');
    }
  };

  const fetchLocationSuggestions = async (query: string) => {
    try {
      const response = await axios.get(
        `https://api.openweathermap.org/geo/1.0/direct?q=${encodeURIComponent(query)}&limit=5&appid=${process.env.REACT_APP_OPENWEATHER_API_KEY}`
      );
      
      if (response.data && response.data.length > 0) {
        setLocationSuggestions(response.data);
        setShowSuggestions(true);
      }
    } catch (error) {
      setLocationSuggestions([]);
    }
  };

  const selectLocation = (suggestion: any) => {
    const newLocation = {
      lat: suggestion.lat,
      lng: suggestion.lon
    };
    setCurrentLocation(newLocation);
    setLocationName(suggestion.name);
    setManualLocation(suggestion.name);
    setShowSuggestions(false);
    
    localStorage.setItem('userLocation', JSON.stringify(newLocation));
    localStorage.setItem('locationTimestamp', Date.now().toString());
  };

  const handleManualLocationSubmit = async () => {
    if (!manualLocation.trim()) return;
    
    setLoading(true);
    try {
      const response = await axios.get(
        `https://api.openweathermap.org/geo/1.0/direct?q=${encodeURIComponent(manualLocation)}&limit=1&appid=${process.env.REACT_APP_OPENWEATHER_API_KEY}`
      );
      
      if (response.data && response.data.length > 0) {
        selectLocation(response.data[0]);
      }
    } catch (error) {
      console.error('Error geocoding location:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchRecommendations = async (budget: number) => {
    setLoading(true);
    try {
      const realPlaces = await fetchRealNearbyPlaces(currentLocation, budget);
      setRecommendations(realPlaces);
    } catch (error) {
      console.error('Error fetching real places:', error);
      setRecommendations(generateFallbackRecommendations(budget));
    } finally {
      setLoading(false);
    }
  };

  const fetchRealNearbyPlaces = async (location: any, budget: number) => {
    if (!location) return generateFallbackRecommendations(budget);
    
    try {
      // Use CORS proxy to access Google Places API
      const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
      const apiUrl = `https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=${location.lat},${location.lng}&radius=5000&type=tourist_attraction&key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}`;
      
      const response = await axios.get(proxyUrl + apiUrl, {
        timeout: 8000,
        headers: {
          'X-Requested-With': 'XMLHttpRequest'
        }
      });
      
      if (response.data && response.data.results && response.data.results.length > 0) {
        const places = response.data.results.slice(0, 4);
        
        const enrichedPlaces = await Promise.all(
          places.map(async (place: any) => {
            const budgetInfo = getBudgetInfo(place, budget);
            const realImage = await fetchRealPlaceImage(place.name, place.types);
            
            return {
              name: place.name,
              cost: budgetInfo.cost,
              type: budgetInfo.type,
              description: place.vicinity || 'Popular local attraction',
              budgetCategory: budgetInfo.category,
              rating: place.rating ? `${place.rating}/5` : 'N/A',
              image: place.photos && place.photos[0] ? 
                `https://maps.googleapis.com/maps/api/place/photo?maxwidth=400&photoreference=${place.photos[0].photo_reference}&key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}` :
                realImage,
              coordinates: {
                lat: place.geometry.location.lat,
                lng: place.geometry.location.lng
              },
              placeId: place.place_id
            };
          })
        );
        
        return enrichedPlaces;
      }
      
      throw new Error('No places found');
    } catch (error) {
      console.log('Google Places API failed, trying alternative approach');
      return await fetchAlternativeRealPlaces(location, budget);
    }
  };
  
  const fetchAlternativeRealPlaces = async (location: any, budget: number) => {
    try {
      // Use Overpass API for real POI data
      const overpassQuery = `
        [out:json][timeout:5];
        (
          node["tourism"~"attraction|museum|gallery|zoo|aquarium"](around:5000,${location.lat},${location.lng});
          node["amenity"~"restaurant|cafe|bar|pub"](around:5000,${location.lat},${location.lng});
          node["leisure"~"park|garden|sports_centre"](around:5000,${location.lat},${location.lng});
        );
        out geom;
      `;
      
      const response = await axios.post(
        'https://overpass-api.de/api/interpreter',
        overpassQuery,
        {
          timeout: 8000,
          headers: { 'Content-Type': 'text/plain' }
        }
      );
      
      if (response.data && response.data.elements && response.data.elements.length > 0) {
        const places = response.data.elements
          .filter((place: any) => place.tags && place.tags.name)
          .slice(0, 4);
        
        const enrichedPlaces = await Promise.all(
          places.map(async (place: any) => {
            const budgetInfo = getBudgetInfoFromTags(place.tags, budget);
            const realImage = await fetchRealPlaceImage(place.tags.name, [budgetInfo.type.toLowerCase()]);
            
            return {
              name: place.tags.name,
              cost: budgetInfo.cost,
              type: budgetInfo.type,
              description: place.tags.description || `${budgetInfo.type} in ${locationName}`,
              budgetCategory: budgetInfo.category,
              rating: 'N/A',
              image: realImage,
              coordinates: {
                lat: place.lat,
                lng: place.lon
              },
              placeId: place.id
            };
          })
        );
        
        return enrichedPlaces;
      }
      
      throw new Error('No alternative places found');
    } catch (error) {
      console.log('Alternative API also failed, using fallback');
      return generateFallbackRecommendations(budget);
    }
  };
  
  const getBudgetInfoFromTags = (tags: any, budget: number) => {
    if (tags.tourism === 'museum' || tags.tourism === 'gallery') {
      return budget <= 500 ? 
        { cost: '50-200', type: 'Museum', category: 'Budget-Friendly' } :
        { cost: '200-500', type: 'Museum', category: 'Mid-Range' };
    }
    
    if (tags.leisure === 'park' || tags.leisure === 'garden') {
      return { cost: '0-50', type: 'Park', category: 'Budget-Friendly' };
    }
    
    if (tags.amenity === 'restaurant' || tags.amenity === 'cafe') {
      return budget <= 500 ? 
        { cost: '100-300', type: 'Dining', category: 'Budget-Friendly' } :
        budget <= 2000 ?
        { cost: '400-800', type: 'Restaurant', category: 'Mid-Range' } :
        { cost: '1000-2500', type: 'Fine Dining', category: 'Premium' };
    }
    
    return budget <= 500 ? 
      { cost: '50-300', type: 'Attraction', category: 'Budget-Friendly' } :
      budget <= 2000 ?
      { cost: '300-800', type: 'Attraction', category: 'Mid-Range' } :
      { cost: '800-2000', type: 'Premium Experience', category: 'Premium' };
  };
  
  const fetchRealPlaceImage = async (placeName: string, types: string[]) => {
    try {
      // Try Pexels API first with place name
      const pexelsResponse = await axios.get(
        `https://api.pexels.com/v1/search?query=${encodeURIComponent(placeName)}&per_page=1&orientation=landscape`,
        {
          headers: {
            'Authorization': process.env.REACT_APP_PEXELS_API_KEY || ''
          },
          timeout: 5000
        }
      );
      
      if (pexelsResponse.data.photos && pexelsResponse.data.photos.length > 0) {
        return pexelsResponse.data.photos[0].src.medium;
      }
      
      // Try Pixabay API as fallback
      const pixabayResponse = await axios.get(
        `https://pixabay.com/api/?key=${process.env.REACT_APP_PIXABAY_API_KEY}&q=${encodeURIComponent(placeName)}&image_type=photo&orientation=horizontal&per_page=3&safesearch=true`,
        { timeout: 5000 }
      );
      
      if (pixabayResponse.data.hits && pixabayResponse.data.hits.length > 0) {
        return pixabayResponse.data.hits[0].webformatURL;
      }
      
      // Try Wikipedia/Wikimedia Commons
      const wikiImage = await fetchWikipediaImage(placeName);
      if (wikiImage) {
        return wikiImage;
      }
      
      // Fallback to type-based search
      return await fetchTypeBasedImage(types);
    } catch (error) {
      return getDefaultImage(types);
    }
  };
  
  const fetchWikipediaImage = async (placeName: string) => {
    try {
      const response = await axios.get(
        `https://en.wikipedia.org/api/rest_v1/page/summary/${encodeURIComponent(placeName)}`,
        { timeout: 5000 }
      );
      
      if (response.data && response.data.thumbnail && response.data.thumbnail.source) {
        return response.data.thumbnail.source;
      }
      return null;
    } catch (error) {
      return null;
    }
  };
  
  const fetchTypeBasedImage = async (types: string[]) => {
    try {
      const typeQuery = getTypeBasedQuery(types);
      
      // Try Pexels with type
      const pexelsResponse = await axios.get(
        `https://api.pexels.com/v1/search?query=${encodeURIComponent(typeQuery)}&per_page=1&orientation=landscape`,
        {
          headers: {
            'Authorization': process.env.REACT_APP_PEXELS_API_KEY || ''
          },
          timeout: 5000
        }
      );
      
      if (pexelsResponse.data.photos && pexelsResponse.data.photos.length > 0) {
        return pexelsResponse.data.photos[0].src.medium;
      }
      
      return getDefaultImage(types);
    } catch (error) {
      return getDefaultImage(types);
    }
  };
  
  const getTypeBasedQuery = (types: string[]) => {
    if (types.includes('museum') || types.includes('gallery')) {
      return 'museum building architecture';
    }
    if (types.includes('park') || types.includes('garden')) {
      return 'public park nature';
    }
    if (types.includes('restaurant') || types.includes('food') || types.includes('dining')) {
      return 'restaurant building exterior';
    }
    if (types.includes('temple') || types.includes('church')) {
      return 'temple building architecture';
    }
    if (types.includes('shopping') || types.includes('mall')) {
      return 'shopping center building';
    }
    if (types.includes('hotel') || types.includes('lodging')) {
      return 'hotel building exterior';
    }
    return 'landmark building architecture';
  };
  
  const getDefaultImage = (types: string[]) => {
    if (types.includes('museum') || types.includes('gallery')) {
      return 'https://images.unsplash.com/photo-1554907984-15263bfd63bd?w=400&h=300&fit=crop';
    }
    if (types.includes('park') || types.includes('garden')) {
      return 'https://images.unsplash.com/photo-1441974231531-c6227db76b6e?w=400&h=300&fit=crop';
    }
    if (types.includes('restaurant') || types.includes('food') || types.includes('dining')) {
      return 'https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?w=400&h=300&fit=crop';
    }
    return 'https://images.unsplash.com/photo-1469474968028-56623f02e42e?w=400&h=300&fit=crop';
  };





  const getBudgetInfo = (place: any, budget: number) => {
    const placeTypes = place.types || [];
    
    if (placeTypes.includes('museum') || placeTypes.includes('art_gallery')) {
      return budget <= 500 ? 
        { cost: '50-200', type: 'Museum/Gallery', category: 'Budget-Friendly' } :
        { cost: '200-500', type: 'Premium Museum', category: 'Mid-Range' };
    }
    
    if (placeTypes.includes('park') || placeTypes.includes('natural_feature')) {
      return { cost: '0-50', type: 'Nature/Park', category: 'Budget-Friendly' };
    }
    
    if (placeTypes.includes('restaurant') || placeTypes.includes('food')) {
      return budget <= 500 ? 
        { cost: '100-300', type: 'Local Dining', category: 'Budget-Friendly' } :
        budget <= 2000 ?
        { cost: '400-800', type: 'Restaurant', category: 'Mid-Range' } :
        { cost: '1000-2500', type: 'Fine Dining', category: 'Premium' };
    }
    
    if (placeTypes.includes('shopping_mall') || placeTypes.includes('store')) {
      return budget <= 1000 ? 
        { cost: '200-600', type: 'Shopping', category: 'Mid-Range' } :
        { cost: '1000-3000', type: 'Premium Shopping', category: 'Premium' };
    }
    
    // Default tourist attraction
    return budget <= 500 ? 
      { cost: '50-300', type: 'Attraction', category: 'Budget-Friendly' } :
      budget <= 2000 ?
      { cost: '300-800', type: 'Popular Attraction', category: 'Mid-Range' } :
      { cost: '800-2000', type: 'Premium Experience', category: 'Premium' };
  };

  const openInGoogleMaps = (place: any) => {
    try {
      const { coordinates, name } = place;
      if (!coordinates || !coordinates.lat || !coordinates.lng) {
        // Fallback: search by name
        const url = `https://www.google.com/maps/search/${encodeURIComponent(name)}`;
        window.open(url, '_blank');
        return;
      }
      const url = `https://www.google.com/maps/search/?api=1&query=${coordinates.lat},${coordinates.lng}`;
      window.open(url, '_blank');
    } catch (error) {
      console.error('Error opening Google Maps:', error);
    }
  };

  const getDirections = (place: any) => {
    try {
      const { coordinates, name } = place;
      if (!coordinates || !coordinates.lat || !coordinates.lng) {
        // Fallback: search by name
        const url = `https://www.google.com/maps/dir/?api=1&destination=${encodeURIComponent(name)}`;
        window.open(url, '_blank');
        return;
      }
      const url = `https://www.google.com/maps/dir/?api=1&destination=${coordinates.lat},${coordinates.lng}`;
      window.open(url, '_blank');
    } catch (error) {
      console.error('Error getting directions:', error);
    }
  };

  const generateBudgetRecommendations = async (budget: number, location: any) => {
    try {
      const response = await axios.post(
        'https://api.groq.com/openai/v1/chat/completions',
        {
          messages: [{
            role: 'user',
            content: `Suggest 4 places/activities for ${budget} budget near ${location?.lat}, ${location?.lng}. Include name, cost, type, description for each.`
          }],
          model: 'mixtral-8x7b-32768',
          max_tokens: 250,
          temperature: 0.7
        },
        {
          headers: {
            'Authorization': `Bearer ${process.env.REACT_APP_GROQ_API_KEY}`,
            'Content-Type': 'application/json'
          }
        }
      );
      
      return generateFallbackRecommendations(budget);
    } catch (error) {
      return generateFallbackRecommendations(budget);
    }
  };

  const generateFallbackRecommendations = (budget: number) => {
    const recommendations = {
      500: [
        { name: 'Local Park', cost: '0-50', type: 'Nature', description: 'Visit beautiful public parks and gardens', budgetCategory: 'Budget-Friendly', image: 'https://images.unsplash.com/photo-1441974231531-c6227db76b6e?w=400&h=300&fit=crop' },
        { name: 'Street Food', cost: '100-200', type: 'Food', description: 'Try authentic local street food', budgetCategory: 'Budget-Friendly', image: 'https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=400&h=300&fit=crop' },
        { name: 'Temple Visit', cost: '20-100', type: 'Heritage', description: 'Explore nearby temples and monuments', budgetCategory: 'Budget-Friendly', image: 'https://images.unsplash.com/photo-1564507592333-c60657eea523?w=400&h=300&fit=crop' },
        { name: 'Local Market', cost: '50-150', type: 'Shopping', description: 'Browse local markets and bazaars', budgetCategory: 'Budget-Friendly', image: 'https://images.unsplash.com/photo-1555529669-e69e7aa0ba9a?w=400&h=300&fit=crop' }
      ],
      1000: [
        { name: 'Museum Tour', cost: '100-300', type: 'Culture', description: 'Discover local history and art', budgetCategory: 'Mid-Range', image: 'https://images.unsplash.com/photo-1554907984-15263bfd63bd?w=400&h=300&fit=crop' },
        { name: 'Restaurant Meal', cost: '400-600', type: 'Dining', description: 'Enjoy meals at popular restaurants', budgetCategory: 'Mid-Range', image: 'https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?w=400&h=300&fit=crop' },
        { name: 'City Tour', cost: '300-500', type: 'Sightseeing', description: 'Guided tour of city highlights', budgetCategory: 'Mid-Range', image: 'https://images.unsplash.com/photo-1469474968028-56623f02e42e?w=400&h=300&fit=crop' },
        { name: 'Shopping Mall', cost: '200-400', type: 'Entertainment', description: 'Shopping and entertainment complex', budgetCategory: 'Mid-Range', image: 'https://images.unsplash.com/photo-1441986300917-64674bd600d8?w=400&h=300&fit=crop' }
      ],
      2000: [
        { name: 'Fine Dining', cost: '800-1500', type: 'Restaurant', description: 'Premium dining experience', budgetCategory: 'Premium', image: 'https://images.unsplash.com/photo-1414235077428-338989a2e8c0?w=400&h=300&fit=crop' },
        { name: 'Spa Treatment', cost: '1000-1800', type: 'Wellness', description: 'Relaxing spa and wellness services', budgetCategory: 'Premium', image: 'https://images.unsplash.com/photo-1544161515-4ab6ce6db874?w=400&h=300&fit=crop' },
        { name: 'Adventure Park', cost: '600-1200', type: 'Adventure', description: 'Thrilling adventure activities', budgetCategory: 'Premium', image: 'https://images.unsplash.com/photo-1558618666-fcd25c85cd64?w=400&h=300&fit=crop' },
        { name: 'Cultural Show', cost: '500-1000', type: 'Entertainment', description: 'Traditional cultural performances', budgetCategory: 'Premium', image: 'https://images.unsplash.com/photo-1493225457124-a3eb161ffa5f?w=400&h=300&fit=crop' }
      ],
      5000: [
        { name: 'Luxury Resort', cost: '3000-5000', type: 'Accommodation', description: 'Premium resort experience', budgetCategory: 'Luxury', image: 'https://images.unsplash.com/photo-1571896349842-33c89424de2d?w=400&h=300&fit=crop' },
        { name: 'Adventure Sports', cost: '2000-4000', type: 'Adventure', description: 'Paragliding, rafting, or similar', budgetCategory: 'Luxury', image: 'https://images.unsplash.com/photo-1551632811-561732d1e306?w=400&h=300&fit=crop' },
        { name: 'Private Tour', cost: '2500-4500', type: 'Sightseeing', description: 'Personalized private tour guide', budgetCategory: 'Luxury', image: 'https://images.unsplash.com/photo-1488646953014-85cb44e25828?w=400&h=300&fit=crop' },
        { name: 'Gourmet Experience', cost: '1500-3000', type: 'Fine Dining', description: 'Multi-course gourmet meals', budgetCategory: 'Luxury', image: 'https://images.unsplash.com/photo-1424847651672-bf20a4b0982b?w=400&h=300&fit=crop' }
      ]
    };
    
    return recommendations[budget as keyof typeof recommendations] || recommendations[1000];
  };

  return (
    <Container
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
    >
      <LocationInput>
        <span style={{color: 'white', fontSize: '0.9rem'}}> Location:</span>
        <div style={{position: 'relative', flex: 1}}>
          <input
            type="text"
            placeholder="Enter city name (e.g., Mumbai, Delhi, Bangalore)"
            value={manualLocation}
            onChange={(e) => {
              setManualLocation(e.target.value);
              if (e.target.value.length > 2) {
                fetchLocationSuggestions(e.target.value);
              } else {
                setShowSuggestions(false);
              }
            }}
            onKeyPress={(e) => e.key === 'Enter' && handleManualLocationSubmit()}
            onFocus={() => manualLocation.length > 2 && setShowSuggestions(true)}
            onBlur={() => setTimeout(() => setShowSuggestions(false), 200)}
          />
          {showSuggestions && locationSuggestions.length > 0 && (
            <SuggestionsList>
              {locationSuggestions.map((suggestion, index) => (
                <SuggestionItem
                  key={index}
                  onClick={() => selectLocation(suggestion)}
                >
                  <div className="city-name">{suggestion.name}</div>
                  <div className="country-name">{suggestion.country} {suggestion.state && `, ${suggestion.state}`}</div>
                </SuggestionItem>
              ))}
            </SuggestionsList>
          )}
        </div>
        <button onClick={handleManualLocationSubmit}>Update Location</button>
        <span style={{color: '#4CAF50', fontSize: '0.85rem'}}>Current: {locationName}</span>
      </LocationInput>

      <Header>
        <Title>
           Budget Recommendations
        </Title>
        <BudgetSelector>
          {budgetOptions.map(option => (
            <BudgetButton
              key={option.value}
              active={selectedBudget === option.value}
              onClick={(e) => {
                e.preventDefault();
                e.stopPropagation();
                setSelectedBudget(option.value);
              }}
            >
              {option.label}
            </BudgetButton>
          ))}
        </BudgetSelector>
      </Header>

      {loading ? (
        <LoadingState>
           Finding best places near {locationName} for your budget...
        </LoadingState>
      ) : (
        <RecommendationGrid>
          {recommendations.map((rec, index) => (
            <RecommendationCard
              key={index}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.3, delay: index * 0.1 }}
            >
              {rec.image ? (
                <img 
                  src={rec.image} 
                  alt={rec.name} 
                  className="place-image"
                  onError={(e) => {
                    const target = e.target as HTMLImageElement;
                    target.style.display = 'none';
                  }}
                />
              ) : (
                <div className="image-placeholder">
                  {rec.type === 'Nature/Park' ? '' :
                   rec.type.includes('Museum') ? '' :
                   rec.type.includes('Food') || rec.type.includes('Dining') ? '' :
                   rec.type.includes('Shopping') ? '' :
                   rec.type.includes('Adventure') ? '' :
                   ''}
                </div>
              )}
              <div className="card-header">
                <div className="place-name">{rec.name}</div>
                <div className="cost-badge">{rec.cost}</div>
              </div>
              <div className="place-type">{rec.type}</div>
              {rec.rating && rec.rating !== 'N/A' && (
                <div className="rating"> {rec.rating}</div>
              )}
              <div className="description">{rec.description}</div>
              <div className="budget-category">
                 {rec.budgetCategory}
              </div>
              
              <div className="action-buttons">
                <button 
                  className="action-button"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    openInGoogleMaps(rec);
                  }}
                >
                   Open in Maps
                </button>
                <button 
                  className="action-button secondary"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    getDirections(rec);
                  }}
                >
                   Get Directions
                </button>

              </div>
            </RecommendationCard>
          ))}
        </RecommendationGrid>
      )}
    </Container>
  );
};

export default BudgetRecommendations;