interface SimpleRouteMapProps {
  origin?: {lat: number, lng: number};
  destination?: {lat: number, lng: number};
  transportMode?: string;
}

const SimpleRouteMap: React.FC<SimpleRouteMapProps> = ({ origin, destination, transportMode = 'driving' }) => {
  const [routeInfo, setRouteInfo] = useState<any>(null);
  const [mapView, setMapView] = useState<'normal' | 'satellite'>('normal');

  useEffect(() => {
    if (origin && destination) {
      calculateRoute();
    }
  }, [origin, destination, transportMode]);

  const calculateRoute = () => {
    if (!origin || !destination) return;

    const distance = calculateDistance(origin.lat, origin.lng, destination.lat, destination.lng);
    const duration = estimateDuration(distance, transportMode);
    
    setRouteInfo({
      distance: `${distance.toFixed(1)} km`,
      duration: duration
    });
  };

  const calculateDistance = (lat1: number, lon1: number, lat2: number, lon2: number) => {
    const R = 6371;
    const dLat = (lat2 - lat1) * Math.PI / 180;
    const dLon = (lon2 - lon1) * Math.PI / 180;
    const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
              Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
              Math.sin(dLon/2) * Math.sin(dLon/2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    return R * c;
  };

  const estimateDuration = (distance: number, mode: string) => {
    const speeds = { walking: 5, bicycling: 15, driving: 50, transit: 30 };
    const speed = speeds[mode as keyof typeof speeds] || speeds.driving;
    const hours = distance / speed;
    const minutes = Math.round(hours * 60);
    
    if (minutes >= 60) {
      const h = Math.floor(minutes / 60);
      const m = minutes % 60;
      return `${h}h ${m}m`;
    } else {
      return `${minutes} min`;
    }
  };

  const openInGoogleMaps = () => {
    if (!origin || !destination) return;
    
    const url = `https://www.google.com/maps/dir/${origin.lat},${origin.lng}/${destination.lat},${destination.lng}`;
    window.open(url, '_blank');
  };

  const openInMaps = () => {
    if (!origin || !destination) return;
    
    const url = `maps:?saddr=${origin.lat},${origin.lng}&daddr=${destination.lat},${destination.lng}`;
    window.location.href = url;
  };

  const getTransportIcon = () => {
    switch (transportMode) {
      case 'walking': return '';
      case 'driving': return '';
      case 'bicycling': return '';
      case 'transit': return '';
      default: return '';
    }
  };

  const shareRoute = () => {
    if (!origin || !destination || !routeInfo) return;
    
    const shareText = `Check out this route: ${routeInfo.distance} in ${routeInfo.duration} via ${transportMode}`;
    if (navigator.share) {
      navigator.share({
        title: 'IntelliTrip Route',
        text: shareText,
        url: window.location.href
      });
    } else {
      navigator.clipboard.writeText(shareText);
      alert('Route details copied to clipboard!');
    }
  };

  return (
    <MapWrapper
      initial={{ opacity: 0, scale: 0.95 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5 }}
    >
      <MapGrid />
      
      <MapContent>
        {origin && destination ? (
          <>
            <div style={{
              position: 'absolute',
              top: '10px',
              right: '10px',
              zIndex: 1000,
              display: 'flex',
              gap: '0.5rem'
            }}>
              <button
                onClick={() => setMapView('normal')}
                style={{
                  background: mapView === 'normal' ? '#00d4ff' : 'rgba(255,255,255,0.8)',
                  color: mapView === 'normal' ? 'white' : 'black',
                  border: 'none',
                  borderRadius: '5px',
                  padding: '0.5rem',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: '600'
                }}
              >
                 Map
              </button>
              <button
                onClick={() => setMapView('satellite')}
                style={{
                  background: mapView === 'satellite' ? '#00d4ff' : 'rgba(255,255,255,0.8)',
                  color: mapView === 'satellite' ? 'white' : 'black',
                  border: 'none',
                  borderRadius: '5px',
                  padding: '0.5rem',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: '600'
                }}
              >
                 Satellite
              </button>
            </div>
            <iframe
              src={mapView === 'normal' 
                ? `https://routing.openstreetmap.de/?z=10&center=${((origin.lat + destination.lat) / 2).toFixed(6)},${((origin.lng + destination.lng) / 2).toFixed(6)}&loc=${origin.lat.toFixed(6)},${origin.lng.toFixed(6)}&loc=${destination.lat.toFixed(6)},${destination.lng.toFixed(6)}&hl=en&alt=0&srv=2`
                : `https://www.google.com/maps/embed/v1/directions?key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}&origin=${origin.lat},${origin.lng}&destination=${destination.lat},${destination.lng}&maptype=satellite`
              }
              width="100%"
              height="100%"
              style={{
                border: 'none',
                borderRadius: '15px'
              }}
              title="Route Map"
              loading="lazy"
            />
          </>
        ) : (
          <div style={{ textAlign: 'center', zIndex: 10 }}>
            <motion.div 
              style={{ fontSize: '5rem', marginBottom: '1rem' }}
              animate={{ rotate: [0, 10, -10, 0] }}
              transition={{ duration: 2, repeat: Infinity }}
            >
              
            </motion.div>
            <div style={{ fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 'bold' }}>Interactive Route Planner</div>
            <div style={{ fontSize: '1rem', opacity: 0.7 }}>Enter origin and destination to visualize your route</div>
          </div>
        )}
      </MapContent>

      {routeInfo && (
        <RouteInfo
          initial={{ y: 100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.5, duration: 0.5 }}
        >
          <div className="route-stats">
            <div className="stat-item">
              <div className="stat-value">{routeInfo.distance}</div>
              <div className="stat-label">Distance</div>
            </div>
            <div className="stat-item">
              <div className="stat-value">{routeInfo.duration}</div>
              <div className="stat-label">Duration</div>
            </div>
            <div className="stat-item">
              <div className="stat-value">{getTransportIcon()}</div>
              <div className="stat-label">{transportMode}</div>
            </div>
          </div>
        </RouteInfo>
      )}
    </MapWrapper>
  );
};

export default SimpleRouteMap;