import React, { useState } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';

const ProfileContainer = styled.div`
  min-height: 100vh;
  background: ${props => props.theme.gradients.dark};
  padding: 2rem;
  margin-left: 280px;
  transition: background 0.3s ease;
  
  @media (max-width: 768px) {
    margin-left: 0;
  }
`;

const ProfileHeader = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  display: flex;
  gap: 2rem;
  align-items: center;
`;

const AvatarSection = styled.div`
  .avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: ${props => props.theme.gradients.primary};
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    font-weight: bold;
    color: white;
    margin-bottom: 1rem;
  }
`;

const UserInfo = styled.div`
  flex: 1;
  
  .name {
    font-size: 2rem;
    color: ${props => props.theme.colors.text};
    margin-bottom: 0.5rem;
  }
  
  .email {
    color: ${props => props.theme.colors.textSecondary};
    margin-bottom: 1rem;
  }
  
  .edit-button {
    background: ${props => props.theme.colors.primary};
    border: none;
    border-radius: 8px;
    padding: 0.5rem 1rem;
    color: white;
    cursor: pointer;
    font-size: 0.9rem;
    transition: background 0.3s ease;
    
    &:hover {
      background: ${props => props.theme.colors.accent};
    }
  }
`;

const SettingsSection = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  
  .section-title {
    font-size: 1.5rem;
    color: ${props => props.theme.colors.text};
    margin-bottom: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
`;

const SettingItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  
  &:last-child {
    border-bottom: none;
  }
  
  .setting-info {
    .setting-name {
      color: ${props => props.theme.colors.text};
      font-weight: 600;
      margin-bottom: 0.25rem;
    }
    
    .setting-desc {
      color: ${props => props.theme.colors.textSecondary};
      font-size: 0.9rem;
    }
  }
`;

const ThemeSelector = styled.div`
  display: flex;
  gap: 0.5rem;
  
  button {
    background: rgba(255,255,255,0.1);
    border: 1px solid rgba(255,255,255,0.2);
    border-radius: 8px;
    padding: 0.5rem 1rem;
    color: white;
    cursor: pointer;
    font-size: 0.85rem;
    transition: all 0.3s;
    
    &.active {
      background: ${props => props.theme.colors.primary};
      border-color: ${props => props.theme.colors.primary};
    }
    
    &:hover {
      background: rgba(255,255,255,0.2);
    }
  }
`;

const EditModal = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  
  .modal-content {
    background: ${props => props.theme.gradients.card};
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255,255,255,0.1);
    border-radius: 20px;
    padding: 2rem;
    width: 90%;
    max-width: 400px;
    
    .modal-title {
      color: ${props => props.theme.colors.text};
      font-size: 1.5rem;
      margin-bottom: 1.5rem;
      text-align: center;
    }
    
    .input-group {
      margin-bottom: 1.5rem;
      
      label {
        display: block;
        color: ${props => props.theme.colors.textSecondary};
        margin-bottom: 0.5rem;
        font-size: 0.9rem;
      }
      
      input {
        width: 100%;
        background: rgba(255,255,255,0.1);
        border: 1px solid rgba(255,255,255,0.2);
        border-radius: 8px;
        padding: 0.75rem;
        color: ${props => props.theme.colors.text};
        font-size: 1rem;
        
        &:focus {
          outline: none;
          border-color: ${props => props.theme.colors.primary};
        }
        
        &::placeholder {
          color: ${props => props.theme.colors.textSecondary};
        }
      }
    }
    
    .modal-actions {
      display: flex;
      gap: 1rem;
      justify-content: flex-end;
      
      button {
        padding: 0.75rem 1.5rem;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        font-size: 0.9rem;
        
        &.cancel {
          background: rgba(255,255,255,0.1);
          color: ${props => props.theme.colors.text};
        }
        
        &.save {
          background: ${props => props.theme.colors.primary};
          color: white;
        }
      }
    }
  }
`;

const StatsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
`;

const StatCard = styled(motion.div)`
  background: ${props => props.theme.gradients.card};
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 15px;
  padding: 1.5rem;
  text-align: center;
  
  .icon {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  .value {
    font-size: 1.8rem;
    font-weight: bold;
    color: ${props => props.theme.colors.primary};
    margin-bottom: 0.25rem;
  }
  
  .label {
    color: ${props => props.theme.colors.textSecondary};
    font-size: 0.9rem;
  }
`;

const ProfilePage: React.FC = () => {
  const { user } = useAuth();
  const { themeMode, setThemeMode } = useTheme();
  const [showEditModal, setShowEditModal] = useState(false);
  const [displayName, setDisplayName] = useState(() => {
    return localStorage.getItem('userDisplayName') || user?.email?.split('@')[0] || 'Travel Enthusiast';
  });
  const [editingName, setEditingName] = useState(displayName);

  const stats = [
    { icon: '', value: '24', label: 'Places Visited' },
    { icon: '', value: '12', label: 'Trips Planned' },
    { icon: '', value: '156', label: 'KM Traveled' },
    { icon: '', value: '8', label: 'Achievements' }
  ];

  const getUserInitials = () => {
    if (displayName) {
      return displayName.charAt(0).toUpperCase();
    }
    if (user?.email) {
      return user.email.charAt(0).toUpperCase();
    }
    return 'U';
  };

  return (
    <ProfileContainer>
      <ProfileHeader
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <AvatarSection>
          <div className="avatar">{getUserInitials()}</div>
        </AvatarSection>
        
        <UserInfo>
          <div className="name">{displayName}</div>
          <div className="email">{user?.email}</div>
          <button className="edit-button" onClick={() => setShowEditModal(true)}>
             Edit Name
          </button>
        </UserInfo>
      </ProfileHeader>

      <SettingsSection
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        <div className="section-title">
           Settings
        </div>
        
        <SettingItem>
          <div className="setting-info">
            <div className="setting-name">Theme</div>
            <div className="setting-desc">Choose your preferred theme</div>
          </div>
          <ThemeSelector>
            <button 
              className={themeMode === 'light' ? 'active' : ''}
              onClick={() => setThemeMode('light')}
            >
               Light
            </button>
            <button 
              className={themeMode === 'dark' ? 'active' : ''}
              onClick={() => setThemeMode('dark')}
            >
               Dark
            </button>
            <button 
              className={themeMode === 'system' ? 'active' : ''}
              onClick={() => setThemeMode('system')}
            >
               System
            </button>
          </ThemeSelector>
        </SettingItem>
      </SettingsSection>

      <StatsGrid>
        {stats.map((stat, index) => (
          <StatCard
            key={index}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: index * 0.1 + 0.4 }}
            whileHover={{ scale: 1.05 }}
          >
            <div className="icon">{stat.icon}</div>
            <div className="value">{stat.value}</div>
            <div className="label">{stat.label}</div>
          </StatCard>
        ))}
      </StatsGrid>
      
      {showEditModal && (
        <EditModal
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={(e) => e.target === e.currentTarget && setShowEditModal(false)}
        >
          <div className="modal-content">
            <div className="modal-title">Edit Display Name</div>
            
            <div className="input-group">
              <label>Display Name</label>
              <input
                type="text"
                value={editingName}
                onChange={(e) => setEditingName(e.target.value)}
                placeholder="Enter your display name"
                autoFocus
              />
            </div>
            
            <div className="modal-actions">
              <button 
                className="cancel"
                onClick={() => {
                  setShowEditModal(false);
                  setEditingName(displayName);
                }}
              >
                Cancel
              </button>
              <button 
                className="save"
                onClick={() => {
                  setDisplayName(editingName);
                  localStorage.setItem('userDisplayName', editingName);
                  setShowEditModal(false);
                }}
              >
                Save
              </button>
            </div>
          </div>
        </EditModal>
      )}
    </ProfileContainer>
  );
};

export default ProfilePage;