const ChatBot: React.FC = () => {
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<any[]>([]);
  const [conversationHistory, setConversationHistory] = useState<string[]>([]);
  const [input, setInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [contextData, setContextData] = useState<any>({});

  useEffect(() => {
    loadContextData();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const loadContextData = async () => {
    try {
      const savedLocation = localStorage.getItem('userLocation');
      const location = savedLocation ? JSON.parse(savedLocation) : { lat: 40.7128, lng: -74.0060 };
      
      // Load weather data if available
      let weatherData = null;
      try {
        weatherData = await getCurrentWeather(location.lat, location.lng || location.lon);
      } catch (error) {
        console.log('Weather data not available');
      }
      
      setContextData({
        user: user?.email?.split('@')[0] || 'User',
        location: location,
        weather: weatherData,
        nearbyPlaces: [],
        appFeatures: {
          routes: ['Walking', 'Driving', 'Cycling', 'Transit', 'Motorcycle'],
          pages: ['Dashboard', 'Explore Destinations', 'Routes & Directions', 'Nearby Places', 'Weather', 'Saved Places'],
          apis: ['OpenWeather', 'Google Maps', 'Wikipedia', 'Unsplash']
        }
      });
    } catch (error) {
      console.error('Error loading context:', error);
      // Set default context
      setContextData({
        user: 'User',
        location: { lat: 40.7128, lng: -74.0060 },
        weather: null,
        nearbyPlaces: [],
        appFeatures: { routes: [], pages: [], apis: [] }
      });
    }
  };



  const generateFallbackRecommendations = (budget: number) => {
    const recommendations = {
      low: [ // 0-500
        { name: 'Local Park Visit', cost: '0-50', type: 'Nature', description: 'Free entry to public parks and gardens', budgetCategory: 'Budget-Friendly' },
        { name: 'Street Food Tour', cost: '100-200', type: 'Food', description: 'Taste local street food and snacks', budgetCategory: 'Budget-Friendly' },
        { name: 'Local Temple/Monument', cost: '20-100', type: 'Heritage', description: 'Visit nearby temples and historical sites', budgetCategory: 'Budget-Friendly' },
        { name: 'Public Transport Tour', cost: '50-150', type: 'Sightseeing', description: 'Explore the city using local transport', budgetCategory: 'Budget-Friendly' }
      ],
      medium: [ // 500-2000
        { name: 'Museum Visit', cost: '100-300', type: 'Culture', description: 'Explore local museums and art galleries', budgetCategory: 'Mid-Range' },
        { name: 'Restaurant Dining', cost: '400-800', type: 'Food', description: 'Dine at popular local restaurants', budgetCategory: 'Mid-Range' },
        { name: 'Auto/Taxi City Tour', cost: '300-600', type: 'Sightseeing', description: 'Guided city tour by auto or taxi', budgetCategory: 'Mid-Range' },
        { name: 'Shopping Mall Visit', cost: '500-1000', type: 'Shopping', description: 'Shopping and entertainment at malls', budgetCategory: 'Mid-Range' }
      ],
      high: [ // 2000+
        { name: 'Premium Restaurant', cost: '1000-2500', type: 'Fine Dining', description: 'Experience fine dining at premium restaurants', budgetCategory: 'Premium' },
        { name: 'Spa & Wellness', cost: '1500-3000', type: 'Wellness', description: 'Relax at luxury spas and wellness centers', budgetCategory: 'Premium' },
        { name: 'Adventure Activities', cost: '2000-5000', type: 'Adventure', description: 'Paragliding, river rafting, or similar activities', budgetCategory: 'Premium' },
        { name: 'Luxury Hotel Stay', cost: '3000-8000', type: 'Accommodation', description: 'Stay at premium hotels and resorts', budgetCategory: 'Premium' }
      ]
    };
    
    if (budget <= 500) return recommendations.low;
    if (budget <= 2000) return recommendations.medium;
    return recommendations.high;
  };

  const sendMessage = async (message?: string) => {
    const messageText = message || input;
    if (!messageText.trim()) return;
    
    const userMessage = { 
      text: messageText, 
      isBot: false, 
      quickActions: [],
      timestamp: new Date().toLocaleTimeString()
    };
    setMessages(prev => [...prev, userMessage]);
    
    // Update conversation history
    setConversationHistory(prev => [...prev.slice(-5), messageText]); // Keep last 6 messages
    setInput('');
    setIsTyping(true);
    
    // Generate AI response with conversation context
    try {
      const botResponse = await generateAIResponse(messageText);
      setMessages(prev => [...prev, botResponse]);
      
      // Update conversation history with bot response
      setConversationHistory(prev => [...prev.slice(-5), botResponse.text]);
      
      setIsTyping(false);
    } catch (error) {
      console.error('Error generating response:', error);
      const errorResponse = {
        text: 'Sorry, I\'m having trouble connecting to my AI service right now. Please try again in a moment.',
        isBot: true,
        timestamp: new Date().toLocaleTimeString()
      };
      setMessages(prev => [...prev, errorResponse]);
      setConversationHistory(prev => [...prev.slice(-5), errorResponse.text]);
      setIsTyping(false);
    }
  };

  const generateAIResponse = async (userInput: string): Promise<any> => {
    try {
      // Get AI response with travel intelligence
      const aiResponse = await getIntelligentTravelResponse(userInput);
      
      // Extract places and fetch images
      const places = extractPlaceNames(aiResponse.text);
      const images = await fetchPlaceImages(places);
      
      // Get nearby places if requested
      const nearbyPlaces = aiResponse.includeNearby ? await fetchNearbyPlaces() : null;
      
      // Get route information if requested
      const routeInfo = aiResponse.includeRoutes ? await getRouteRecommendations(userInput) : null;
      
      return {
        text: aiResponse.text,
        isBot: true,
        images: images,
        places: nearbyPlaces,
        routes: routeInfo,
        quickActions: generateSmartActions(userInput, aiResponse.text),
        timestamp: new Date().toLocaleTimeString()
      };
      
    } catch (error) {
      console.error('AI Error:', error);
      return generateFallbackResponse(userInput);
    }
  };



  const getIntelligentTravelResponse = async (userInput: string) => {
    const systemPrompt = `You are an expert travel assistant for IntelliTrip app. Provide helpful, detailed travel information.
    
    Context:
    - User location: ${contextData.location?.lat}, ${contextData.location?.lng}
    - Weather: ${contextData.weather ? `${contextData.weather.temperature}C, ${contextData.weather.description}` : 'Unknown'}
    
    Guidelines:
    1. Be conversational and friendly
    2. Provide specific, actionable advice
    3. Include budget estimates in Indian Rupees ()
    4. Mention specific place names for better context
    5. Keep responses under 150 words
    
    Focus areas: destinations, routes, budget, weather, food, accommodation`;
    
    try {
      const response = await fetch('https://api.groq.com/openai/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${process.env.REACT_APP_GROQ_API_KEY}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          messages: [
            { role: 'system', content: systemPrompt },
            { role: 'user', content: userInput }
          ],
          model: 'llama-3.1-8b-instant',
          max_tokens: 250,
          temperature: 0.8,
          top_p: 0.9
        })
      });
      
      if (!response.ok) {
        throw new Error(`Groq API error: ${response.status}`);
      }
      
      const data = await response.json();
      const aiText = data.choices?.[0]?.message?.content || 'I can help you with travel planning!';
      
      return {
        text: aiText.trim(),
        includeNearby: userInput.toLowerCase().includes('nearby') || userInput.toLowerCase().includes('around'),
        includeRoutes: userInput.toLowerCase().includes('route') || userInput.toLowerCase().includes('direction')
      };
    } catch (error) {
      console.error('Groq API error:', error);
      throw error;
    }
  };

  const getGeminiAIResponse = async (userInput: string): Promise<string> => {
    const systemPrompt = `You are an intelligent, friendly travel assistant for IntelliTrip app. 
    
    Context:
    - User location: ${contextData.location?.lat}, ${contextData.location?.lng}
    - Previous conversation: ${conversationHistory.slice(-3).join(' | ')}
    - Current weather: ${contextData.weather ? `${contextData.weather.temperature}C, ${contextData.weather.description}` : 'Not available'}
    
    Instructions:
    - Be conversational and helpful
    - Reference previous messages when relevant
    - Mention specific place names for image generation
    - Keep responses under 200 words
    - Include practical travel tips
    - Ask follow-up questions to continue conversation`;
    
    const conversationContext = messages.slice(-4).map(msg => `${msg.isBot ? 'Assistant' : 'User'}: ${msg.text}`).join('\n');
    const fullPrompt = `${systemPrompt}\n\nConversation Context:\n${conversationContext}\n\nUser: ${userInput}\n\nAssistant:`;
    
    const response = await fetch(`https://generativelanguage.googleapis.com/v1beta/models/gemini-pro:generateContent?key=${process.env.REACT_APP_GEMINI_API_KEY}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        contents: [{
          parts: [{ text: fullPrompt }]
        }],
        generationConfig: {
          temperature: 0.8,
          maxOutputTokens: 300
        }
      })
    });
    
    if (!response.ok) {
      throw new Error('Gemini API request failed');
    }
    
    const data = await response.json();
    return data.candidates?.[0]?.content?.parts?.[0]?.text || 'I apologize, but I\'m having trouble processing your request right now.';
  };

  const extractPlaceNames = (text: string): string[] => {
    // Extract place names using common patterns
    const placePatterns = [
      /\b[A-Z][a-z]+ [A-Z][a-z]+(?:\s+[A-Z][a-z]+)*\b/g, // Proper nouns
      /\b(?:visit|see|explore|go to)\s+([A-Z][a-z]+(?:\s+[A-Z][a-z]+)*)/gi,
      /\b([A-Z][a-z]+(?:\s+[A-Z][a-z]+)*)\s+(?:is|are|has|have)/gi
    ];
    
    const places = new Set<string>();
    
    placePatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        matches.forEach(match => {
          const cleaned = match.replace(/^(visit|see|explore|go to)\s+/i, '').trim();
          if (cleaned.length > 3 && cleaned.length < 50) {
            places.add(cleaned);
          }
        });
      }
    });
    
    return Array.from(places).slice(0, 3); // Limit to 3 places
  };

  const fetchPlaceImages = async (placeNames: string[]): Promise<any[]> => {
    if (placeNames.length === 0) return [];
    
    const imagePromises = placeNames.slice(0, 4).map(async (place) => {
      try {
        // Try Unsplash first
        const unsplashResponse = await fetch(
          `https://api.unsplash.com/search/photos?query=${encodeURIComponent(place + ' travel')}&per_page=1&client_id=${process.env.REACT_APP_UNSPLASH_ACCESS_KEY}`
        );
        
        if (unsplashResponse.ok) {
          const data = await unsplashResponse.json();
          if (data.results?.[0]) {
            return {
              place,
              url: data.results[0].urls.small,
              alt: place
            };
          }
        }
        
        // Fallback to Pexels
        const pexelsResponse = await fetch(
          `https://api.pexels.com/v1/search?query=${encodeURIComponent(place)}&per_page=1`,
          { headers: { 'Authorization': process.env.REACT_APP_PEXELS_API_KEY || '' } }
        );
        
        if (pexelsResponse.ok) {
          const data = await pexelsResponse.json();
          if (data.photos?.[0]) {
            return {
              place,
              url: data.photos[0].src.medium,
              alt: place
            };
          }
        }
        
        return null;
      } catch (error) {
        return null;
      }
    });
    
    const results = await Promise.all(imagePromises);
    return results.filter(Boolean);
  };

  const fetchNearbyPlaces = async () => {
    if (!contextData.location) return null;
    
    try {
      const response = await fetch(
        `https://cors-anywhere.herokuapp.com/https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=${contextData.location.lat},${contextData.location.lng}&radius=5000&type=tourist_attraction&key=${process.env.REACT_APP_GOOGLE_PLACES_API_KEY}`
      );
      
      if (response.ok) {
        const data = await response.json();
        return data.results?.slice(0, 3).map((place: any) => ({
          name: place.name,
          rating: place.rating || 'N/A',
          type: place.types?.[0]?.replace('_', ' ') || 'Attraction'
        }));
      }
    } catch (error) {
      console.error('Error fetching nearby places:', error);
    }
    
    return null;
  };

  const getRouteRecommendations = async (userInput: string) => {
    const lowerInput = userInput.toLowerCase();
    
    const routes = [
      { mode: 'walking', icon: '', duration: 'Available', description: 'Pedestrian routes' },
      { mode: 'driving', icon: '', duration: 'Available', description: 'Car routes with traffic' },
      { mode: 'transit', icon: '', duration: 'Available', description: 'Public transportation' },
      { mode: 'cycling', icon: '', duration: 'Available', description: 'Bike-friendly paths' }
    ];
    
    if (lowerInput.includes('walk')) return [routes[0]];
    if (lowerInput.includes('drive') || lowerInput.includes('car')) return [routes[1]];
    if (lowerInput.includes('bus') || lowerInput.includes('train')) return [routes[2]];
    if (lowerInput.includes('bike') || lowerInput.includes('cycle')) return [routes[3]];
    
    return routes;
  };

  const generateSmartActions = (userInput: string, aiResponse: string): string[] => {
    const lowerInput = userInput.toLowerCase();
    const lowerResponse = aiResponse.toLowerCase();
    
    // Extract mentioned places from AI response for contextual actions
    const mentionedPlaces = extractPlaceNames(aiResponse);
    
    if (mentionedPlaces.length > 0) {
      const place = mentionedPlaces[0];
      return [
        `Weather in ${place}`,
        `Budget for ${place}`,
        `Routes to ${place}`,
        'More destinations'
      ];
    }
    
    return generateQuickActions(userInput);
  };

  const generateQuickActions = (userInput: string): string[] => {
    const lowerInput = userInput.toLowerCase();
    
    if (lowerInput.includes('weather')) {
      return ['What about tomorrow?', 'Best time to visit?', 'Packing suggestions?'];
    }
    if (lowerInput.includes('budget') || lowerInput.includes('cost')) {
      return ['Show me cheaper options', 'What about luxury?', 'Food budget tips?'];
    }
    if (lowerInput.includes('place') || lowerInput.includes('destination')) {
      return ['Tell me more details', 'How to get there?', 'Best time to visit?'];
    }
    if (lowerInput.includes('route') || lowerInput.includes('direction')) {
      return ['Alternative routes?', 'Public transport?', 'Travel time?'];
    }
    if (lowerInput.includes('food') || lowerInput.includes('restaurant')) {
      return ['Local specialties?', 'Street food options?', 'Fine dining?'];
    }
    if (lowerInput.includes('hotel') || lowerInput.includes('stay')) {
      return ['Budget hotels?', 'Luxury resorts?', 'Best areas to stay?'];
    }
    
    // Context-aware suggestions based on conversation
    const recentTopics = conversationHistory.slice(-2).join(' ').toLowerCase();
    if (recentTopics.includes('paris')) {
      return ['More about Paris?', 'Paris weather?', 'Paris budget tips?'];
    }
    if (recentTopics.includes('japan')) {
      return ['More about Japan?', 'Japanese culture?', 'Japan travel tips?'];
    }
    
    return ['Tell me about Paris', 'Budget 1000 for India', 'Weather updates', 'Plan my route'];
  };

  const generateFallbackResponse = (userInput: string) => {
    const smartResponse = getSmartResponse(userInput);
    const lowerInput = userInput.toLowerCase();
    
    // Weather responses with real data
    if (lowerInput.includes('weather') || lowerInput.includes('temperature')) {
      const weatherText = contextData.weather ? 
        `Current weather: ${contextData.weather.temperature}C, ${contextData.weather.description} in ${contextData.weather.location}. Feels like ${contextData.weather.feelsLike}C with ${contextData.weather.humidity}% humidity.` :
        "I can help you check weather conditions! Use the Weather section to get real-time weather data for any destination.";
      
      return {
        text: weatherText,
        isBot: true,
        quickActions: ['View weather page', 'Check forecast', 'Weather alerts'],
        timestamp: new Date().toLocaleTimeString()
      };
    }
    
    // Default fallback response
    return {
      text: smartResponse,
      isBot: true,
      quickActions: generateQuickActions(userInput),
      timestamp: new Date().toLocaleTimeString()
    };
  };



  const renderMessage = (msg: any, idx: number) => {
    return (
      <Message key={idx} isBot={msg.isBot}>
        {msg.isBot && (
          <div className="ai-badge">
             AI Response
          </div>
        )}
        <div>{msg.text}</div>
        
        {msg.images && msg.images.length > 0 && (
          <div className="image-gallery">
            {msg.images.map((image: any, i: number) => (
              <div key={i} className="image-card">
                <img 
                  src={image.url} 
                  alt={image.alt}
                  onError={(e) => {
                    e.currentTarget.style.display = 'none';
                  }}
                />
                <div className="image-label">{image.place}</div>
              </div>
            ))}
          </div>
        )}
        
        {msg.routes && (
          <div>
            {msg.routes.map((route: any, i: number) => (
              <div key={i} className="route-suggestion">
                <div className="route-header">
                  <span>{route.icon}</span>
                  <span>{route.mode}</span>
                </div>
                <div className="route-details">
                  {route.duration}  {route.distance}
                </div>
              </div>
            ))}
          </div>
        )}
        
        {msg.places && (
          <div>
            {msg.places.map((place: any, i: number) => (
              <div key={i} className="place-card">
                {place.image && <img src={place.image} alt={place.name} style={{width: '50px', height: '40px', objectFit: 'cover', borderRadius: '4px'}} />}
                <div className="place-info">
                  <div className="place-name">{place.name}</div>
                  <div className="place-details"> {place.rating}  {place.type}</div>
                </div>
              </div>
            ))}
          </div>
        )}
        
        {msg.weather && (
          <div className="route-suggestion">
            <div className="route-header">
              <span></span>
              <span>{msg.weather.current}</span>
            </div>
            <div className="route-details">{msg.weather.details}</div>
          </div>
        )}
        
        {msg.budgetRecommendations && (
          <div>
            {msg.budgetRecommendations.map((rec: any, i: number) => (
              <BudgetCard key={i}>
                <div className="budget-header">
                  <div className="place-name">{rec.name}</div>
                  <div className="budget-range">{rec.cost}</div>
                </div>
                <div className="place-details">{rec.type}  {rec.description}</div>
                <div className="cost-breakdown"> {rec.budgetCategory}</div>
              </BudgetCard>
            ))}
          </div>
        )}
        
        {msg.quickActions && (
          <QuickActions>
            {msg.quickActions.map((action: string, i: number) => (
              <ActionButton key={i} onClick={() => {
                if (action.includes('page')) {
                  const page = action.toLowerCase().replace('view ', '').replace(' page', '');
                  window.location.href = `/${page}`;
                } else if (['Dashboard', 'Explore destinations', 'Routes & directions', 'Nearby places'].includes(action)) {
                  const routes = {
                    'Dashboard': '/dashboard',
                    'Explore destinations': '/explore', 
                    'Routes & directions': '/routes',
                    'Nearby places': '/nearby'
                  };
                  window.location.href = routes[action as keyof typeof routes] || '/dashboard';
                } else {
                  sendMessage(action);
                }
              }}>
                {action}
              </ActionButton>
            ))}
          </QuickActions>
        )}
        
        {msg.timestamp && (
          <div className="timestamp">
            {msg.timestamp}
          </div>
        )}
      </Message>
    );
  };

  return (
    <ChatContainer>
      <AnimatePresence>
        {isOpen && (
          <ChatWindow
            initial={{ opacity: 0, scale: 0.8, y: 20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.8, y: 20 }}
          >
            <ChatHeader>
              <div>
                <h4> AI Travel Assistant</h4>
                <small> Powered by Groq AI</small>
              </div>
              <button onClick={() => setIsOpen(false)} style={{background: 'none', border: 'none', color: 'white', fontSize: '1.2rem'}}>
                
              </button>
            </ChatHeader>
            
            <Messages>
              {messages.map(renderMessage)}
              {isTyping && (
                <Message isBot={true}>
                  <div className="ai-badge">
                     AI Response
                  </div>
                  <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
                    <div style={{ 
                      width: '20px', 
                      height: '20px', 
                      border: '2px solid #00d4ff', 
                      borderTop: '2px solid transparent', 
                      borderRadius: '50%', 
                      animation: 'spin 1s linear infinite' 
                    }}></div>
                    <span>Thinking about your request...</span>
                  </div>
                </Message>
              )}
            </Messages>
            
            <InputContainer>
              <input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && !isTyping && sendMessage()}
                placeholder={isTyping ? "AI is thinking..." : "Ask me anything about travel..."}
                disabled={isTyping}
              />
              <button 
                onClick={() => sendMessage()}
                disabled={isTyping || !input.trim()}
                style={{
                  opacity: isTyping || !input.trim() ? 0.5 : 1,
                  cursor: isTyping || !input.trim() ? 'not-allowed' : 'pointer'
                }}
              >
                {isTyping ? '...' : 'Send'}
              </button>
            </InputContainer>
          </ChatWindow>
        )}
      </AnimatePresence>
      
      <ChatToggle
        onClick={() => setIsOpen(!isOpen)}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        
      </ChatToggle>
    </ChatContainer>
  );
};

export default ChatBot;