import React from 'react';
import styled, { keyframes } from 'styled-components';
import { motion } from 'framer-motion';

const pulse = keyframes`
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.7; }
`;

const wave = keyframes`
  0%, 60%, 100% { transform: initial; }
  30% { transform: translateY(-15px); }
`;

const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

const LoaderContainer = styled(motion.div)`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  padding: 2rem;
`;

const DotsLoader = styled.div`
  display: flex;
  gap: 8px;
  margin-bottom: 1rem;
  
  .dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: ${props => props.theme.colors.primary};
    animation: ${wave} 1.4s ease-in-out infinite;
    
    &:nth-child(1) { animation-delay: -0.32s; }
    &:nth-child(2) { animation-delay: -0.16s; }
    &:nth-child(3) { animation-delay: 0s; }
  }
`;

const CircleLoader = styled.div`
  width: 40px;
  height: 40px;
  border: 3px solid ${props => props.theme.colors.text === '#0f172a' ? 'rgba(30,64,175,0.1)' : 'rgba(0,212,255,0.1)'};
  border-top: 3px solid ${props => props.theme.colors.primary};
  border-radius: 50%;
  animation: ${spin} 1s linear infinite;
  margin-bottom: 1rem;
`;

const PulseLoader = styled.div`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: ${props => props.theme.gradients.primary};
  animation: ${pulse} 2s ease-in-out infinite;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
`;

const LoadingText = styled.div`
  color: ${props => props.theme.colors.text};
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  margin-top: 0.5rem;
`;

const ProgressBar = styled.div`
  width: 200px;
  height: 4px;
  background: ${props => props.theme.colors.text === '#0f172a' ? 'rgba(0,0,0,0.1)' : 'rgba(255,255,255,0.1)'};
  border-radius: 2px;
  overflow: hidden;
  margin-top: 1rem;
  
  .progress {
    height: 100%;
    background: ${props => props.theme.gradients.primary};
    border-radius: 2px;
    animation: ${keyframes`
      0% { width: 0%; }
      50% { width: 70%; }
      100% { width: 100%; }
    `} 2s ease-in-out infinite;
  }
`;

interface ModernLoaderProps {
  type?: 'dots' | 'circle' | 'pulse' | 'progress';
  text?: string;
  icon?: string;
}

const ModernLoader: React.FC<ModernLoaderProps> = ({ 
  type = 'dots', 
  text = 'Loading...', 
  icon = '' 
}) => {
  const renderLoader = () => {
    switch (type) {
      case 'circle':
        return <CircleLoader />;
      case 'pulse':
        return <PulseLoader>{icon}</PulseLoader>;
      case 'progress':
        return (
          <>
            <PulseLoader>{icon}</PulseLoader>
            <ProgressBar>
              <div className="progress" />
            </ProgressBar>
          </>
        );
      default:
        return <DotsLoader>
          <div className="dot" />
          <div className="dot" />
          <div className="dot" />
        </DotsLoader>;
    }
  };

  return (
    <LoaderContainer
      initial={{ opacity: 0, scale: 0.8 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.3 }}
    >
      {renderLoader()}
      <LoadingText>{text}</LoadingText>
    </LoaderContainer>
  );
};

export default ModernLoader;